require('@/assets/css/index.css');
require('@/assets/font/iconfont.css');
import Vue from 'vue';
import 'mint-ui/lib/style.min.css'
import { DatetimePicker, Picker,InfiniteScroll,Popup,Switch } from 'mint-ui';
Vue.component('mt-datetime-picker', DatetimePicker);
Vue.component('mt-picker', Picker);
Vue.component('mt-popup', Popup);
Vue.component('mt-switch', Switch);
Vue.use(InfiniteScroll);


//引入config路径
require('./config');

import M from './common';


export default {
  M
}



