import Vue from 'vue'
import Router from 'vue-router'
import Home from '@/pages/Coupon/Home'
import MyCoupon from '@/pages/Coupon/MyCoupon'
import CloseAccount from '@/pages/Coupon/CloseAccount'
import UseCoupon from '@/pages/Coupon/UseCoupon'
import CouponDetail from '@/pages/Coupon/CouponDetail'
import HomeDetail from '@/pages/Coupon/HomeDetail'
import WriteOffs from '@/pages/Coupon/WriteOffs'
import Accredit from '@/pages/Coupon/Accredit'
import Recommend from '@/pages/Coupon/Recommend'
import WithDraw from '@/pages/Coupon/WithDraw'
import ShowDetails from '@/pages/Coupon/ShowDetails'
import RecommendDetail from '@/pages/Coupon/RecommendDetail'


import NonmemberHome from '@/pages/Nonmember/NonmemberHome'



import Login from '@/pages/Login'

import GetFail from '@/components/GetFail'



Vue.use(Router)

var router=new Router({
  routes: [
    {
      path: '/home/:busId/:tuijianCode',
      component: Home,
      meta:{
        title: '优惠券领取'
      }
    },
    {
      path: '/home/:busId',
      name: 'home',
      component: Home,
      meta:{
        title: '优惠券领取'
      }
    },
    {
      path: '/login/:busId',
      name: 'login',
      component: Login,
      meta:{
        title: '优惠券领取'
      }
    },
    {
      path: '/mycoupon/:busId',
      name: 'mycoupon',
      component: MyCoupon,
      meta:{
        title: '我的优惠券'
      }
    },
    {
      path: '/closeaccount/:busId',
      name: 'closeaccount',
      component: CloseAccount,
      meta:{
        title: '结算详情'
      }
    },
    {
      path: '/getfail/:busId',
      name: 'getfail',
      component: GetFail,
      meta:{
        title: '我的优惠券'
      }
    },
    {
      path: '/usecoupon/:busId',
      name: 'usecoupon',
      component: UseCoupon,
      meta:{
        title: '优惠券'
      }
    },
    {
      path: '/coupondetail/:busId',
      name: 'coupondetail',
      component: CouponDetail,
      meta:{
        title: '优惠券详情'
      }
    },
    {
      path: '/homedetail/:busId',
      name: 'homedetail',
      component: HomeDetail,
      meta:{
        title: '优惠券详情'
      }
    },
    {
      path: '/writeoffs/:busId/:code',
      name: 'writeoffs',
      component: WriteOffs,
      meta:{
        title: '优惠券核销'
      }
    },
    {
      path: '/recommend/:busId',
      name: 'recommend',
      component: Recommend,
      meta:{
        title: '推荐'
      }
    },,
    {
      path: '/recommenddetail/:busId',
      name: 'recommenddetail',
      component: RecommendDetail,
      meta:{
        title: '推荐'
      }
    },
    {
      path: '/nonmemberhome/:busId',
      name: 'nonmemberhome',
      component: NonmemberHome,
      meta:{
        title: '优惠券领取'
      }
    },
    {
      path: '/withdraw/:busId',
      name: 'withdraw',
      component: WithDraw,
      meta:{
        title: '提现'
      }
    },
    {
      path: '/showdetails/:busId',
      name: 'showdetails',
      component: ShowDetails,
      meta:{
        title: '提现详情'
      }
    },
    {
      path: '/accredit/:busId/:shopId',
      name: 'accredit',
      component: Accredit,
      meta:{
        title: '核销授权'
      }
    }
  ]
});
router.beforeEach((to, from, next) => {
  next()

});
router.afterEach((transition) => {
  header.setTitle(transition.meta.title)
});
export default router;
var header = {
  /**
   * 修改页面标题
   * @param title 标题
   */
  setTitle(title) {
    document.title = title;
    var mobile = navigator.userAgent.toLowerCase()
    if (/iphone|ipad|ipod/.test(mobile)) {
      var iframe = document.createElement('iframe')
      iframe.style.display = 'none'
      var iframeCallback = function() {
        setTimeout(function() {
          iframe.removeEventListener('load', iframeCallback)
          document.body.removeChild(iframe)
        }, 0)
      }
      iframe.addEventListener('load', iframeCallback)
      document.body.appendChild(iframe)
    }
  }
}



