<template>
	<div class="couponfoot clearfix"  >
		<div class="home fl w30 " :class="[nowPos==1?'active':'']" @click="myhome(1)">
			<p> <i class="icon iconfont icon-ticket"></i>领券</p>
		</div>
		<div class="mycoupon tuijian fl w30 " :class="[nowPos==2?'active':'']" @click="myhome(2)">
			<p>  <i class="icon iconfont icon-tuijian"></i>推荐</p>
		</div><div class="mycoupon fr w30 " :class="[nowPos==3?'active':'']" @click="myhome(3)">
			<p><i class="icon iconfont icon-qiaquanbao"></i>我的优惠券</p>
		</div>
	</div>
</template>

<script>
export default {
	data () {
	    return {
	      ishome:false,
	      isMycoupon:true
	    }
	},
	props: ['nowPos'],
	methods:{
		// youfeiquan(){
		// 	var _this=this;
		// 	_this.ishome=false;
		// 	_this.isMycoupon=true;
		// 	_this.$router.push({ name: 'mycoupon'});
			
		// },
		myhome(cur){
			var _this=this;
			_this.ishome=cur
			_this.$emit('go-whichRoute',_this.ishome);
			if(cur==1){
				setTimeout(function(){
					_this.$router.push({ name: 'home'});
				},26)
			}else if(cur==2){
				setTimeout(function(){
					_this.$router.push({ name: 'recommenddetail'});
				},26)
			}else{
				setTimeout(function(){
					_this.$router.push({ name: 'mycoupon'});
				},26)
			}
			
		}
	},
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
	
</style>



