<template>
	<div class="accredit">
	   	<div v-show="issucceed" >
	   		<img src="@/assets/images/10.jpg">
	   		<p>授权成功</p>
	   	</div>
	   	<div v-show="isfail">
	   		<img src="@/assets/images/11.jpg">
	   		<p>{{failMsg}}</p>
	   	</div>
	   	<div v-show="havegot">
	   		<img src="@/assets/images/12.jpg">
	   		<p>该账户已授权</p>
	   	</div>
	</div>
</template>

<script>
import CouponFooter from '@/components/CouponFooter'
import Lib from '@/assets/js/Lib';
export default {
	data () {
	    return {
	     	isfail:false,
	     	issucceed:false,
	     	havegot:false,
	     	busId:0,
	     	code:0,
	    	url:'',
	    	shopId:6,
	    	failMsg:'',
	    	havegotMsg:'',
	     	// isbg:this.bgColor
	    }
	},
	props:{
		bgColor:true
	},
  	components:{
	    CouponFooter
	},
	methods:{
		loadData(){
			var _this=this,json={};
			_this.busId= _this.$route.params.busId;
			_this.shopId= _this.$route.params.shopId;
			_this.url=window.location.href;
			json["busId"]=_this.busId;
			json["requestUrl"]=_this.url;
			json["shopId"]=_this.shopId;
			Lib.M.ajax({
				'url':memberAPI.activeAPI.addAuthorization_POST,
                'type': 'POST',
                'data':{
                   "json":json
                },
                'success':function (data){
                	if(data.code==0){
                		_this.issucceed=true;
                	}
                	
                },
                'warning':function(err){
            		_this.isfail=true;
            		_this.failMsg=err.msg;
                	if(data.code==6012){
                		_this.havegot=true;
                		_this.havegotMsg=err.msg;
                	}
                },
                'error':function(err){
                	Lib.M.tipsMsg('请求数据失败');
                }

            })
		},
		
	},
	created:function(){
		var _this=this;
        _this.loadData();
    },
	beforeCreate () {
	    window.document.querySelector('body').setAttribute('style', 'background:#fff')
	},
	beforeDestroy () {
	    // window.document.querySelector('body').setAttribute('style', '')
	}
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>

</style>



