<template>
	<div class="couponDetail">
	   	<div class="couponDetailTitle clearfix">
   			<div class="w18 fl">
	   			<p>详细信息</p>
	   		</div>
	   		<div class="conDetial w75 fl">
				<ul class="conDetialul">
					<li class="useCContentT" v-if="curCardData.useType==0"><span></span>全场通用</li>
					<li class="useCContentT" v-if="curCardData.cardType==4"><span></span>{{curCardData.defaultDetail}}</li>
					<li class="useCContentT" v-if="curCardData.cardType==1"><span></span>{{curCardData.discount}}折</li>
	   				<li class="useCContentT" v-if="curCardData.gift!=null"><span></span>{{curCardData.gift}}</li>
					<li v-if="curCardData.cardType==2&&curCardData.cashLeastCost>0"><span></span>消费满{{curCardData.cashLeastCost}}元可减{{curCardData.reduceCost}}元</li>
				</ul>
			</div>
		</div>
	   	<div class="couponDetailTitle clearfix">
   			<div class="w18 fl">
	   			<p>有效日期</p>
	   		</div>
	   		<div class="conDetial w75 fl">
				<ul class="conDetialul">
					<li>{{curCardData.beginTimestamp}}~{{curCardData.endTimestamp}}</li>
				</ul>
			</div>
		</div>
	   	<div class="couponDetailTitle clearfix">
   			<div class="w18 fl">
	   			<p>可用时段</p>
	   		</div>
	   		<div class="conDetial conDetialBottom w75 fl">
				<ul class="conDetialul" v-if="duofenCardTimeData==null">
					<li v-if="curCardData.useTime==0"><span></span>全时段可用</li>
					<li v-if="curCardData.gift!=null"><span></span>{{curCardData.gift}}</li>
				</ul>
				<ul class="conDetialul" v-else>
					<li v-if="curCardData.useTime==0"><span></span>全时段可用</li>
					<li v-if="curCardData.useTime==1"><span></span> {{duofenCardTimeData.week}}可用</li>
					<li v-if="duofenCardTimeData.holidays==1"><span></span>法定节假日不可用</li>
					<li v-if="duofenCardTimeData.otherTime==1" ><span></span><div v-html="duofenCardTimeData.otherTimeSets"></div></li>
					<li v-if="curCardData.gift!=null"><span></span>{{curCardData.gift}}</li>
				</ul>
			</div>
		</div>
	   	<div class="couponDetailTitle clearfix">
   			<div class="w18 fl">
	   			<p>联系电话</p>
	   		</div>
	   		<div class="conDetial w75 fl">
				<ul class="conDetialul">
					<li>{{curCardData.servicePhone}}</li>
				</ul>
			</div>
		</div>
		<div class="couponDetailTitle clearfix">
   			<div class="w18 fl">
	   			<p>使用说明</p>
	   		</div>
	   		<div class="conDetial instructions w75 fl" v-html="curCardData.description">
				
			</div>
		</div>
		<div class="graphicDetail">
			<h3>图文详情</h3>
			<div class="graphicDetailImg" v-for="(item,index) in textImageListData">
				<img :src="imgPath+item.imageDetail" >
				<div class="describe ">
					{{item.textDetail}}
				</div>
			</div>
		</div>
	</div>
</template>

<script>
import CouponFooter from '@/components/CouponFooter'
import Lib from '@/assets/js/Lib';
export default {
	data () {
	    return {
	    	busId:0,
	    	url:'',
	    	cardId:0,
	    	curCardData:[],
	    	duofenCardTimeData:[],
	    	imgPath:'',
	    	textImageListData:[],
	    }
	},
  	components:{
	    CouponFooter
	},
	methods:{
		loadData(){
			var _this=this,json={};
			_this.busId= _this.$route.params.busId;
			_this.cardId= _this.$route.query.cardId;
			_this.url=window.location.href;
			json["busId"]=_this.busId;
			json["requestUrl"]=_this.url;
			json["cardId"]=_this.cardId;
			Lib.M.ajax({
				'url':memberAPI.activeAPI.findDuofenCardDetailsByCardId_POST,
                'type': 'POST',
                'data':{
                   "json":json
                },
                'success':function (data){
                	if(data.code==0){
	                	_this.curCardData=data.data.duofenCardNew;
	                	_this.textImageListData=JSON.parse(_this.curCardData.textImageList);
		                _this.duofenCardTimeData=data.data.duofenCardTime;
		                _this.imgPath=data.data.path;
		                _this.duofenCardTimeData=Lib.M.weekSwitch(_this.duofenCardTimeData,_this.curCardData.useTime)
                	}
                	console.log('购买详情',data);
                },
                'warning':function(err){
                	Lib.M.tipsMsg("获取失败");
                }
            })
		}
		
	},
	created:function(){
        this.loadData();
    },
	beforeCreate () {
	    window.document.querySelector('body').setAttribute('style', 'background:#fff')
	},
	beforeDestroy () {
	    // window.document.querySelector('body').setAttribute('style', '')
	}
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>

</style>



