	   	</div>
	   	<div v-show="!bgColor">
	   		<img src="static/images/1.png">
	   		<p>领取成功</p>
	   	</div>
	</div>
</template>

<script>
import CouponFooter from '@/components/CouponFooter'
export default {
	data () {
	    return {
	     	islogin:false,
	     	ismember:false,
	     	hasMessage:true,
	     	jifen:false,
	     	fenbi:false,
	     	// isbg:this.bgColor
	    }
	},
	props:{
		bgColor:true
	},
  	components:{
	    CouponFooter
	},
	methods:{
		
	},
	beforeCreate () {
	    window.document.querySelector('body').setAttribute('style', 'background:#fffae1')
	},
	beforeDestroy () {
	    // window.document.querySelector('body').setAttribute('style', '')
	}
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
	.getfail img{
		width: 60%;
		margin:2rem 0 0.5rem 0;
	}
	.getfail p{
		font-size: 0.5rem;
	}
</style>



