
<template>
	<div class="myhome">
	    <div class="head" >
	    	<div class="headtile">
	    		<span class="headImg fl">
	    		 	<i class="icon iconfont icon-touxiang" v-if="ismember"></i>
	    		 	<img :src="headimgurl" class="memberImg" v-else>
	    		</span>
				<div v-if="ismember" class="detial fl">
					<p>{{phone}}</p>
					<span @click="cweihuiyuan">成为会员</span>
				</div>
				<div v-else class="detial fl">
					<p>{{nickName}}<span class="member isactive">会员</span></p>
					<span>15213241235</span>
				</div>
	    	</div>
	    </div>
	    <div class="component">
	    	<div v-if="hasMessage">
		    	<div class="category clearfix" v-for="(item,index) in allCardData">
		    		<div class="categoryImg  clearfix" :class="[(item.cardBgState==0)?'tongyong':'']" >
		    			<div class="categoryLeft  fl" :class="[(item.cardType==1)?'zhekouname':'']">
		    				<p v-if="item.cardType==2">¥<span class="free">{{item.cardName}}</span></p>
		    				<p v-else>{{item.cardName}}</p>
		    			</div>
		    			<div class="categoryRight fl ">
		    				<p class="title">{{item.title}}</p>
		    				<p class="time">有效期：{{item.startTime}}~{{item.endTime}}<span class="use" @click.stop="useCoupon(index,'my')">去使用</span></p>
		    				<div class="details">
		    					<p>全场通用<span @click.stop="viewDetail(index+1,'my')"> <i class="icon iconfont" :class="[(hasDetial==(index+1))?'icon-open':'icon-pack-up']"></i></span></p>
		    				</div>
		    			</div>
		    		</div>
		    		<div class="conDetial detialShow" v-show="hasDetial==(index+1)">
	    				<ul class="conDetialul" v-if="duofenCardTimeData==null">
							<li v-if="curCardData.useType==0">全场通用</li>
							<li v-if="curCardData.useTime==0">全时段可用</li>
							<li v-if="curCardData.gift!=null">{{curCardData.gift}}</li>
						</ul>
						<ul class="conDetialul" v-else>
							<li v-if="curCardData.useType==0">全场通用</li>
							<li v-if="curCardData.useTime==0">全时段可用</li>
							<li v-if="curCardData.useTime==1"> {{duofenCardTimeData.week}}可用</li>
							<li v-if="duofenCardTimeData.holidays==1">法定节假日不可用</li>
							<li v-if="duofenCardTimeData.otherTime==1" v-html="duofenCardTimeData.otherTimeSets"></li>
							<li v-if="curCardData.gift!=null">{{curCardData.gift}}</li>
						</ul>
	    			</div>
		    	</div>
		    	<div class="category clearfix" v-if="hasShare">
		    		<p class="part2"> 朋友共享</p>
		    		<div v-for="(item,index) in duofenFriendData">
			    		<div class="categoryImg clearfix" :class="[(item.cardBgState==0)?'tongyong':'']">
			    			<div class="categoryLeft  fl" :class="[(item.cardType==1)?'zhekouname':'']">
			    				<p v-if="item.cardType==2">¥<span class="free">{{item.cardName}}</span></p>
			    				<p v-else>{{item.cardName}}</p>
			    			</div>
			    			<div class="categoryRight fl ">
			    				<p class="title">{{item.title}}</p>
			    				<p class="time">有效期：{{item.startTime}}~{{item.endTime}}<span class="use" @click.stop="useCoupon(index,'friend')">去使用</span></p>
			    				<div class="details">
			    					<p>全场通用<span @click.stop="viewDetail(index+1,'friend')"> <i class="icon iconfont" :class="[(hasDetialP1==(index+1))?'icon-open':'icon-pack-up']"></i></span></p>
			    				</div>
			    			</div>
			    		</div>
			    		<div class="conDetial detialShow" v-show="hasDetialP1==(index+1)">
		    				<ul class="conDetialul" v-if="duofenCardTimeData==null">
								<li v-if="curCardData.useType==0">全场通用</li>
								<li v-if="curCardData.useTime==0">全时段可用</li>
								<li v-if="curCardData.gift!=null">{{curCardData.gift}}</li>
							</ul>
							<ul class="conDetialul" v-else>
								<li v-if="curCardData.useType==0">全场通用</li>
								<li v-if="curCardData.useTime==0">全时段可用</li>
								<li v-if="curCardData.useTime==1"> {{duofenCardTimeData.week}}可用</li>
								<li v-if="duofenCardTimeData.holidays==1">法定节假日不可用</li>
								<li v-if="duofenCardTimeData.otherTime==1" v-html="duofenCardTimeData.otherTimeSets"></li>
								<li v-if="curCardData.gift!=null">{{curCardData.gift}}</li>
							</ul>
		    			</div>
		    		</div>
		    	</div>
		    	<div class="viewlose" v-if="!isViewLose">
		    		<p @click.stop="viewlose">查看已失效的券</p>
		    	</div>
		    	<div class="category" v-if="isViewLose">
		    		<p class="part3"> <span>已失效的券</span></p>
		    		<div v-for="(item,index) in loseData">
			    		<div class="categoryImg clearfix" :class="[(item.state==1)?'yishiyong':((item.state==2)?'yiguoqi':'')]">
			    			<div class="categoryLeft  fl" :class="[(item.cardType==1)?'zhekouname':'']">
			    				<p v-if="item.cardType==2">¥<span class="free">{{item.cardName}}</span></p>
			    				<p v-else>{{item.cardName}}</p>
			    			</div>
			    			<div class="categoryRight loseRight fl ">
			    				<p class="title">{{item.title}}</p>
			    				<p v-if="curCardData.gift!=null">{{item.gift}}</p>
			    				<p class="time">有效期：{{item.beginTimestamp}}~{{item.endTimestamp}}</p>
			    			</div>
			    		</div>
		    		</div>
		    	</div>
	    	</div>
	    	<div class="nomessage" v-else>
	    		<p>您还未登录，暂时无法查看更多优惠券信息</p>
	    	</div>
	    </div>
	   <div class="heightline"></div>
		<CouponFooter v-on:go-whichRoute="goWhich" :nowPos="whichPos"></CouponFooter>
	</div>
</template>

<script>
import CouponFooter from '@/components/CouponFooter'
import Lib from '@/assets/js/Lib';
export default {
	data () {
	    return {
	     	ismember:true,
	     	hasMessage:true,
	     	hasDetial:false,
	     	hasDetialP1:false,

	     	whichPos:3,
	     	isViewLose:false,
	     	busId:0,
	     	url:'',
	     	nickName:'',
	     	isHuiyuan:0,
	     	headimgurl:'',
	     	phone:'',
	     	huiyuanUrl:'',
	     	DuofenCardData:[],
	     	DuofenCardDatas:[],
	     	allCardData:[],
	     	duofenFriendData:[],
	     	duofenCardTimeData:[],
	     	duofenCardGetNewData:[],
	     	curCardData:[],
	     	loseData:[],
	     	cardId:0,
	     	hasShare:false,
	    }
	},
  	components:{
	    CouponFooter
	},
	methods:{
		loadData(){
			var _this=this,json={};
			_this.busId= _this.$route.params.busId;
			_this.url=window.location.href;
			json["busId"]=_this.busId;
			json["requestUrl"]=_this.url;
			Lib.M.ajax({
				'url':memberAPI.activeAPI.myDuofenCard_POST,
                'type': 'POST',
                'data':{
                   "json":json
                },
                'success':function (data){
                	if(data.code==0){
                		_this.isHuiyuan=data.data.isHuiyuan;
                		_this.headimgurl=data.data.headimgurl;
                		_this.phone=data.data.phone;
                		_this.huiyuanUrl=data.data.lingquUrl;
                		_this.ismember=(_this.isHuiyuan==1)?false:true;
                		_this.DuofenCardDatas=data.data.myDuofenCard;
                		_this.duofenFriendData=data.data.duofenFriends;
                		_this.nickName=data.data.nickName;
                		// debugger;
                		_this.allCardData=_this.roundData(_this.DuofenCardDatas);
                		if(_this.duofenFriendData!=undefined){
                			_this.duofenFriendData=_this.roundData(_this.duofenFriendData);
                			if(_this.duofenFriendData.length>0){
	                			_this.hasShare=true;
	                		}
                		}
                		
                	}
                	console.log("我的优惠券",data)
                	
                },
                'warning':function(err){
                	if(err.code==5026){
                		let cardIds={};
                		cardIds['cardId']=_this.cardId;
                		cardIds['isBuy']=_this.isBuy;
                		_this.$router.push({ name: 'login',query:{cardIds:cardIds}});
                	}
                	if(err.code==6007){
                		_this.$router.push({ name: 'closeaccount',params:{busId:_this.busId}});
                	}
                	Lib.M.tipsMsg(err.msg);
                },
                'error':function(err){
                	Lib.M.tipsMsg('请求数据失败');
                }

            })
		},
		roundData(data){
			let _this=this,cardType,date = new Date().getTime(),allCardData=[];
			if(data.length!=0||data.length!=null){
        		for(let i=0;i<data.length;i++){
        			cardType=data[i].cardType;
        			if(data[i].useType==0){
        				data[i].cardBgState=0;
        			}
        			if(data[i].limitType==1){
        				data[i].cardBgState=1;
        				if(data[i].lingquOver==1){
            				data[i].cardBgState=2;
            			}
        			}
        			if(data[i].limitType==0){
        				if(data[i].lingquOver==1){
            				data[i].cardBgState=2;
            			}
            			if(data[i].lingquOver==0){
            				data[i].cardBgState=0;
            			}
        			}
        			switch (cardType){ 
						case 1 : //折扣券
							data[i].cardName=data[i].discount+'折';
							break; 
						case 2 ://代金券
							data[i].cardName=data[i].reduceCost;

							break; 
						case 3: //礼品券
							data[i].cardName='礼品券';
							break; 
						case 4://通用券
							data[i].cardName='优惠券';

							break; 
						default :
							break; 
					}

                	data[i].startTime=Lib.M.formatTime(data[i].startTime,true);
                	data[i].endTime=Lib.M.formatTime(data[i].endTime,true);
        			allCardData.push(data[i]);
        		}
        		return allCardData;
    		}
		},
		
		useCoupon(pos,part){
			var _this=this;
			if(part=='my'){
				_this.cardId=_this.allCardData[pos].receiveId;
				_this.$router.push({ name: 'usecoupon',query:{cardId:_this.cardId}});
				
			}
			if(part=='friend'){
				_this.cardId=_this.duofenFriendData[pos].receiveId;
				_this.$router.push({ name: 'usecoupon',query:{cardId:_this.cardId}});
			}
			
		},
		viewDetail(pos,part){
			var _this=this,data,hdz;
			hdz=part+pos;
			data=JSON.parse(sessionStorage.getItem(hdz));
			if(data!=null){
				_this.curCardData=data.duofenCardNew;
            	_this.duofenCardTimeData=data.duofenCardTime;
            	_this.duofenCardGetNewData=data.duofenCardGetNew;
        		_this.duofenCardTimeData=Lib.M.weekSwitch(_this.duofenCardTimeData,_this.curCardData.useTime)
        		_this.duofenCardGetNewData.startTime=Lib.M.formatTime(_this.duofenCardGetNewData.startTime,true);
        		_this.duofenCardGetNewData.endTime=Lib.M.formatTime(_this.duofenCardGetNewData.endTime,true);
        		if(part=='my'){
					_this.hasDetial=(_this.hasDetial==pos)?false:pos;
					_this.hasDetialP1=false;
				}
				if(part=='friend'){
					_this.hasDetialP1=(_this.hasDetialP1==pos)?false:pos;
					_this.hasDetial=false;
				}
			}else{
				if(part=='my'){
					_this.cardId=_this.allCardData[pos-1].receiveId;
					_this.curCouponDetail(_this.cardId,pos,part);
				}
				if(part=='friend'){
					_this.cardId=_this.duofenFriendData[pos-1].receiveId;
					_this.curCouponDetail(_this.cardId,pos,part);	
				}
			}
		},
		curCouponDetail(cardId,pos,part){
			var _this=this,json={},cdc;
			cdc=part+pos;
			json["busId"]=_this.busId;
			json["requestUrl"]=_this.url;
			json["receiveId"]=cardId;
			Lib.M.ajax({
				'url':memberAPI.activeAPI.useDuofenCardByCardId_POST,
                'type': 'POST',
                'data':{
                   "json":json
                },
                'success':function (data){
                	if(data.code==0){
                		_this.curCardData=data.data.duofenCardNew;
	                	_this.duofenCardTimeData=data.data.duofenCardTime;
	                	_this.duofenCardGetNewData=data.data.duofenCardGetNew;
                		_this.duofenCardTimeData=Lib.M.weekSwitch(_this.duofenCardTimeData,_this.curCardData.useTime)
                		_this.duofenCardGetNewData.startTime=Lib.M.formatTime(_this.duofenCardGetNewData.startTime,true);
                		_this.duofenCardGetNewData.endTime=Lib.M.formatTime(_this.duofenCardGetNewData.endTime,true);
                		sessionStorage.setItem(cdc,JSON.stringify(data.data));
                		if(part=='my'){
							_this.hasDetial=(_this.hasDetial==pos)?false:pos;
							_this.hasDetialP1=false;
						}
						if(part=='friend'){
							_this.hasDetialP1=(_this.hasDetialP1==pos)?false:pos;
							_this.hasDetial=false;
						}

                	}
                	console.log("使用",data)
                	
                },
                'warning':function(err){
                	Lib.M.tipsMsg(err.msg);
                },
                'error':function(err){
                	Lib.M.tipsMsg('请求数据失败');
                }

            })
		},
		goWhich(pos){
			var _this=this;
			if(pos){
				_this.whichPos=pos;
			}
		},
		viewlose(){
			var _this=this,json={};
			json["busId"]=_this.busId;
			json["requestUrl"]=_this.url;
			Lib.M.ajax({
				'url':memberAPI.activeAPI.invalidDuofenCard_POST,
                'type': 'POST',
                'data':{
                   "json":json
                },
                'success':function (data){
                	if(data.code==0){
                		_this.loseData=data.data;
                		_this.loseData=_this.roundData(_this.loseData);
                		console.log('失效',data)
                	}
                }
			})
			_this.isViewLose=true;

		},
		cweihuiyuan(){
			var _this=this;
			window.location.href=_this.huiyuanUrl;
		}
	},
	created:function(){
		var _this=this;
        _this.loadData();
    },
	beforeCreate () {
	    window.document.querySelector('body').setAttribute('style', 'background:#f5f5f5')
	},
	beforeDestroy () {
		sessionStorage.clear();
	    // window.document.querySelector('body').setAttribute('style', '')
	}
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>

</style>



