<template>
	<div class="vipcnwind">
		<div class="applystoretitle">
			<span class="fr clearfix" @click="closeVipcn"><i class="icon iconfont icon-cha"></i></span>
		</div>
		<div class="qrCode vipcnImg">
			<img class="qrCodeImg" :src="guanzhuUrl">
			<p class="qrCodeImgP qrCodeP2">长按图片 【识别二维码】 关注公众号 </p>
		</div>
		<div class="vipcnOtherWay vipcnTip">
	    	<p>你还可以</p>
	    	<ul>
	    		<li>打开微信，点击‘添加朋友’</li>
	    		<li>搜索公众号：duofriend</li>
	    		<li>点击”关注“，完成</li>
	    	</ul>
	    </div>
	</div>
</template>

<script>

export default {
	data () {
		return {
	    	isCloseVipcn:true
	    }
	},
  	components:{

	},
	props: ['guanzhuUrl'],
	methods:{
		closeVipcn(){
        	var _this=this;
        	_this.$emit('close-vipcnWind',_this.isCloseVipcn);
	    },

	},
	created () {
	    // window.document.querySelector('body').setAttribute('style', 'background:#f3f3f3')
	},
	beforeDestroy () {
	    // document.querySelector('body').setAttribute('style', '')
	}
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>

</style>



