<template>
	<div class="myhome">
	   	<div class="clearfix topuphead">
			<a @click="$router.go(-1)" class="ago fl"><i class="icon iconfont icon-left_atn"></i>返回</a>
			<router-link tag="a"  :to="{ name: 'home'}" class="more fr" >
				<a >首页</a>
			</router-link>
		</div>
	    <div class="accountcont">
			<div class="accountcontTit clearfix">
				<div class="titImg fl w25">
					<img :src="pathImg">
				</div>
				<div class="titDetail fl w55">	
					<p class="titname">{{duofenCardNewData.cardName}}</p>
					<p class="titnamedetail">{{duofenCardNewData.title}}</p>
					<p class="titnameprice">¥<span>{{leftBuyMoney}}</span>.<span>{{rightBuyMoney}}</span></p>
				</div>
			</div>
			<!-- <i class="icon iconfont "></i> -->
			<div class="accountpart clearfix">
				<span class="fl">折扣信息</span>
				<span class="fr" @click="discountInfo"> <i class="icon iconfont" :class="[isdiscount?'icon-top-1-copy':'icon-at_atn']"></i></span>
			</div>
			<div v-show="isdiscount">
				<div class="accountpart clearfix">
					<span class="fl">积分抵扣</span>
					<span class="fr"> <mt-switch v-model="jifen" @change="choosejifen" :disabled="isjifendisabled"></mt-switch></span>
				</div>
				<div class="accountpart clearfix">
					<span class="fl">粉币抵扣</span>
					<span class="fr"><mt-switch v-model="fenbi" @change="choosefenbi" :disabled="isfenbidisabled"></mt-switch></span>
				</div>
			</div>
			<div class="accountpart accountpartDetail clearfix">
				<p class="clearfix"><span class="fl">优惠券金额</span><span class="fr">¥{{leftBuyMoney}}.{{rightBuyMoney}}</span></p>
				<p class="clearfix" :class="{isLast:(!jifen&&!fenbi)}"><span class="fl">会员折扣</span><span class="fr">－¥{{discountMoney}}</span></p>
				<p class="clearfix" :class="{isLast:!jifen}" v-show="fenbi"><span class="fl">粉币折扣</span><span class="fr">－¥{{useFenbi}}</span></p>
				<p class="clearfix" :class="{isLast:jifen}" v-show="jifen"><span class="fl">积分折扣</span><span class="fr">－¥{{useJifen}}</span></p>
			</div>
			<div class="accountpart accountpartTotal clearfix">
				<p class="fr accountpartTotalP"><span class="span1">小计:</span><span class="span4">¥</span><span class="span2">{{leftMoney}}</span><span class="span3">.{{rightMoney}}</span></p>
				<!-- <p class="titnameprice">¥<span>88</span> .<span>00</span></p> -->
			</div>
	    </div>
	    <!-- 支付方式 -->
	    <div class="buyWay">
	    	<div class="accountpart clearfix">
				<span class="fl">支付方式</span>
				<span class="fr"> {{payWayName}}<i class="icon iconfont icon-right_atn" @click="chooseBuyWay"></i></span>
			</div>
	    </div>
	    <mt-popup
		  	v-model="ischoosebuy"
		  	position="bottom" class="buyWayWin" :closeOnClickModal="false">
		  	<div class="buyList">
		  		<ul>
		  			<li class="title">支付方式<span class="fr"> <i class="icon iconfont icon-cha" @click="closeBuyWin"></i></span></li>
		  			<li v-for="(item,index) in payTypes">
		  				<div class="accountpart clearfix" @click="changeBuyWay(index)">
							<span class="fl"><i class="icon iconfont" :class="[(item.payType==2)?'icon-zhifubao':((item.payType==5)?'icon-chuzhiqia':'icon-weixinzhifu')]"></i>{{item.name}}支付</span>
							<span class="fr" ><i class="icon iconfont icon-right_atn"></i></span>
						</div>
		  			</li>
		  		</ul>
		  	</div>
		</mt-popup>
	    <!-- <div class="accoutline"></div> -->
	    <div class="accoutWin" >
		    <div class="accoutfoot clearfix"  >
				<div class="accoutleft fl w60 ">
					<p class="heji">合计：<span>¥{{leftMoney}}.{{rightMoney}}</span></p>
					<p class="zonge">总额：<span class="buyMoney">¥{{leftBuyMoney}}.{{rightBuyMoney}}</span> 总优惠：<span>¥{{offertotal}}</span></p>
				</div>
				<div class="accoutright fr w30 " >
					<p @click="closeAccount">去结算</p>
				</div>
			</div>
		</div>
	</div>
</template>

<script>
import CouponFooter from '@/components/CouponFooter'
import Lib from '@/assets/js/Lib';
export default {
	data () {
	    return {
	     	ismember:false,
	     	hasMessage:true,
	     	jifen:false,
	     	fenbi:false,
	     	isdiscount:false,
	     	ischoosebuy:false,
	     	cardId:'',
	     	busId:0,
	     	url:'',
	     	duofenCardNewData:[],
	     	duofenCardPublishData:[],
	     	payTypes:[],
	     	pathImg:'',
	     	buyMoney:0,
	     	usehuiyuanquanyi:0,
	     	fenbiMoney:0,
	     	jifenMoney:0,
	     	ctId:0,
	     	discount:'',
	     	discountMoney:0,
	     	endMoney:0,
	     	leftMoney:0,
	     	rightMoney:0,
	     	offertotal:0,
	     	leftBuyMoney:0,
	     	rightBuyMoney:0,
	     	isjifendisabled:false,
	     	isfenbidisabled:false,
	     	tuijianCode:'',
	     	resultMoney:0,
	     	isputIn:false,
	     	nowNumber:0,
	     	isUseFenbi:0,
	     	isUseJifen:0,
	     	payWayName:'',
	     	payType:'',
	     	/*
	     		积分计算 integral：总共积分，jifenRatio：兑换比例
	     		jifenMoney：总共积分/兑换比例
	     		jifenStartMoney:最低兑换限制大于该设置允许兑换
	     	 */
	     	integral:0,
	     	jifenMoney:0,
	     	jifenRatio:0,
	     	jifenStartMoney:0,
	     	useJifen:0,
	     	nowJifen:0,
	     	/*
	     		积分计算 fans_currency：总共积分，fenbiRatio：兑换比例
	     		fenbiMoney：总共积分/兑换比例
	     		fenbiStartMoney:最低兑换限制大于该设置允许兑换
	     	 */
	     	fans_currency:0,
	     	fenbiMoney:0,
	     	fenbiRatio:0,
	     	fenbiStartMoney:0,
	     	useFenbi:0,
	     	nowFenbi:0,
	    }
	},
  	components:{
	    CouponFooter
	},
	methods:{
		loadData(){
			var _this=this,json={};
			_this.busId= _this.$route.params.busId;
			_this.cardId= _this.$route.query.cardId;
			_this.tuijianCode= _this.$route.query.tuijianCode;
			_this.url=window.location.href;
			json["busId"]=_this.busId;
			json["requestUrl"]=_this.url;
			json["cardId"]=_this.cardId;
			Lib.M.ajax({
				'url':memberAPI.activeAPI.findBuyDuofenCardDetails_POST,
                'type': 'POST',
                'data':{
                   "json":json
                },
                'success':function (data){
                	if(data.code==0){
                		_this.duofenCardNewData=data.data.duofenCardNew;
                		_this.duofenCardPublishData=data.data.duofenCardPublish;
                		_this.pathImg=data.data.path+_this.duofenCardNewData.loginUrl;
                		_this.cardType=_this.duofenCardNewData.cardType;
                		_this.usehuiyuanquanyi=data.data.usehuiyuanquanyi;
                		_this.buyMoney=data.data.buyMoney.toFixed(2);
                		_this.payTypes=data.data.payTypes;
                		if(_this.payTypes!=undefined){
                			_this.payWayName=_this.payTypes[0].name;
                			_this.payType=_this.payTypes[0].payType;
                		}
                		_this.ctId=data.data.ctId;
                		if(_this.usehuiyuanquanyi==0){
							_this.isfenbidisabled=true;
							_this.isjifendisabled=true;
							var moneyresult=Lib.M.isDot(_this.buyMoney);
							_this.discountMoney=_this.discountMoney.toFixed(2);
		                	_this.leftBuyMoney=moneyresult[0];
			            	_this.rightBuyMoney=moneyresult[1];
			            	_this.offertotal=_this.discountMoney;
						}else{
							var moneyresult=Lib.M.isDot(_this.buyMoney);
		                	_this.leftBuyMoney=moneyresult[0];
			            	_this.rightBuyMoney=moneyresult[1];
							if(_this.duofenCardPublishData.isJifen==0){
								_this.isjifendisabled=true;
							}
							if(_this.duofenCardPublishData.isFenbi==0){
								_this.isfenbidisabled=true;
							}
							if(_this.duofenCardPublishData.isDiscount==1){
								switch (_this.ctId){ 
									case 2 ://折扣卡
										if(data.data.discount!=undefined){
											_this.discount=data.data.discount;
										}
										break; 
									case 3: //储值卡
										if(data.data.fuCardDiscount!=undefined){
											_this.discount=data.data.fuCardDiscount;
										}
										break; 
									default :
										break; 
								}
							}else{
								_this.discount=10;
							}
							// 积分兑换计算
							_this.integral=data.data.integral;
							_this.jifenRatio=data.data.jifenRatio;
							_this.jifenStartMoney=data.data.jifenStartMoney;
							if((_this.integral/_this.jifenRatio)>=_this.jifenStartMoney){
								_this.jifenMoney=data.data.jifenMoney;
							}else{
								_this.isjifendisabled=true;
							}
							// 粉币兑换计算
							_this.fans_currency=data.data.fans_currency;
							_this.fenbiRatio=data.data.fenbiRatio;
							_this.fenbiStartMoney=data.data.fenbiStartMoney;
							if((_this.fans_currency/_this.fenbiRatio)>=_this.fenbiStartMoney){
								_this.fenbiMoney=data.data.fenbiMoney;
							}else{
								_this.isfenbidisabled=true;
							}
							if(_this.discount!= "" || _this.discount != null || _this.discount!= undefined){
								_this.discountMoney=(_this.buyMoney)*((10-_this.discount)/10);
								_this.discountMoney=(Math.round(_this.discountMoney*100))/100;
								let resultDis=Lib.M.isDot(_this.discountMoney)
								
								_this.discountMoney=resultDis.join('.');
							}
							_this.resultMoney=(_this.buyMoney-_this.discountMoney).toFixed(2);
		                	let result=Lib.M.isDot(_this.resultMoney)
		                	_this.leftMoney=result[0];
			            	_this.rightMoney=result[1];
		                	_this.offertotal=_this.discountMoney;
						}





                		switch (_this.cardType){ 
							case 1 : //折扣券
								_this.duofenCardNewData.cardName="折扣券";
								break; 
							case 2 ://代金券
								_this.duofenCardNewData.cardName="代金券";

								break; 
							case 3: //礼品券
								_this.duofenCardNewData.cardName='礼品券';
								break; 
							case 4://通用券
								_this.duofenCardNewData.cardName='优惠券';
								break; 
							default :
								break; 
						}
                	}
                	
                	console.log('优惠券购买',data)
                }
			})
		},
		buyCoupon(){
			var _this=this;
			_this.isdetial=true
		},
		closeWin(){
			var _this=this;
			_this.isdetial=false
		},
		discountInfo(){
			var _this=this;
			_this.isdiscount=!_this.isdiscount;
		},
		choosejifen(){
			var _this=this;
			if(_this.jifen){
				if(_this.buyMoney==0){
					return
				}
				_this.isUseJifen=1;
				if(_this.fenbi){
					if(_this.jifenMoney>=_this.useFenbi){
						_this.useJifen=_this.useFenbi;
						_this.leftMoney=0;
		            	_this.rightMoney='00';
		            	_this.offertotal=_this.buyMoney;
					}else{
						_this.useJifen=Lib.M.isDot(_this.jifenMoney);
						_this.useJifen=(Number(_this.useJifen[0])+Number(_this.useJifen[1])).toFixed(2);
						_this.nowNumber=_this.useFenbi-_this.jifenMoney;
						_this.nowJifen=Lib.M.isDot(_this.nowNumber)
						_this.leftMoney=_this.nowJifen[0];
		            	_this.rightMoney=_this.nowJifen[1];
		            	_this.offertotal=(Number(_this.discountMoney)+Number(_this.useJifen)+Number(_this.fenbiMoney)).toFixed(2);
					}
					return
				}
				if(_this.jifenMoney>=_this.resultMoney){
					_this.useJifen=_this.resultMoney;
					_this.leftMoney=0;
	            	_this.rightMoney='00';
	            	_this.offertotal=(Number(_this.discountMoney)+Number(_this.useJifen)).toFixed(2);
	            	_this.buyMoney=0;
				}else{
					_this.useJifen=Lib.M.isDot(_this.jifenMoney);
					_this.useJifen=(Number(_this.useJifen[0])+Number(_this.useJifen[1])).toFixed(2);
					_this.nowNumber=_this.resultMoney-_this.jifenMoney;
					_this.nowJifen=Lib.M.isDot(_this.nowNumber)
					_this.leftMoney=_this.nowJifen[0];
	            	_this.rightMoney=_this.nowJifen[1];
	            	_this.offertotal=(Number(_this.discountMoney)+Number(_this.jifenMoney)).toFixed(2);
				}
			}else{
				_this.useJifen='0.00';
				_this.isUseJifen=0;
				if(_this.fenbi){
					_this.nowJifen=Lib.M.isDot(_this.useFenbi)
					_this.leftMoney=_this.nowJifen[0];
	            	_this.rightMoney=_this.nowJifen[1];
	            	_this.offertotal=_this.discountMoney;
	            	_this.buyMoney=_this.nowNumber;
	            	return
				}
				_this.buyMoney=_this.resultMoney;
				_this.nowJifen=Lib.M.isDot(_this.resultMoney)
				_this.leftMoney=_this.nowJifen[0];
            	_this.rightMoney=_this.nowJifen[1];
            	_this.offertotal=_this.discountMoney;
			}
			
		},
		choosefenbi(){
			var _this=this;
			if(_this.fenbi){
				if(_this.buyMoney==0){
					return
				}
				_this.isUseFenbi=1;
				if(_this.jifen){
					if(_this.fenbiMoney>=_this.useJifen){
						_this.useFenbi=_this.useJifen;
						_this.leftMoney=0;
		            	_this.rightMoney='00';
		            	_this.offertotal=_this.buyMoney;
					}else{
						_this.useFenbi=Lib.M.isDot(_this.fenbiMoney);
						_this.useFenbi=(Number(_this.useFenbi[0])+Number(_this.useFenbi[1])).toFixed(2);
						_this.nowNumber=_this.useJifen-_this.fenbiMoney;
						_this.nowFenbi=Lib.M.isDot(_this.nowNumber)
						_this.leftMoney=_this.nowFenbi[0];
		            	_this.rightMoney=_this.nowFenbi[1];
		            	_this.offertotal=(Number(_this.discountMoney)+Number(_this.jifenMoney)+Number(_this.useFenbi)).toFixed(2);
					}
					return
				}
				if(_this.fenbiMoney>=_this.resultMoney){
					_this.useFenbi=_this.fenbiMoney;
					_this.leftMoney=0;
	            	_this.rightMoney='00';
	            	_this.offertotal=(Number(_this.discountMoney)+Number(_this.useFenbi)).toFixed(2);
	            	_this.buyMoney=0;
				}else{
					_this.useFenbi=Lib.M.isDot(_this.fenbiMoney);
					_this.useFenbi=(Number(_this.useFenbi[0])+Number(_this.useFenbi[1])).toFixed(2);
					_this.nowNumber=_this.resultMoney-_this.fenbiMoney;
					_this.nowFenbi=Lib.M.isDot(_this.nowNumber)
					_this.leftMoney=_this.nowFenbi[0];
	            	_this.rightMoney=_this.nowFenbi[1];
	            	_this.offertotal=(Number(_this.discountMoney)+Number(_this.useFenbi)).toFixed(2);
				}
			}else{
				_this.useFenbi='0.00';
				_this.isUseFenbi=0;
				if(_this.jifen){
					_this.nowJifen=Lib.M.isDot(_this.useJifen)
					_this.leftMoney=_this.nowJifen[0];
	            	_this.rightMoney=_this.nowJifen[1];
	            	_this.offertotal=_this.discountMoney;
	            	_this.buyMoney=_this.nowNumber;
	            	return
				}
				_this.buyMoney=_this.resultMoney;
				_this.nowFenbi=Lib.M.isDot(_this.resultMoney)
				_this.leftMoney=_this.nowFenbi[0];
            	_this.rightMoney=_this.nowFenbi[1];
            	_this.offertotal=_this.discountMoney;
			}
			
		},
		closeAccount(){
			var _this=this,json={},qianduanMoney='';
			if(_this.isputIn){
				return
			}
			qianduanMoney=`${_this.leftMoney}.${_this.rightMoney}`;
			json["busId"]=_this.busId;
			json["requestUrl"]=_this.url;
			json["cardId"]=_this.cardId;
			json["usehuiyuanquanyi"]=_this.usehuiyuanquanyi;
			json["useFenbi"]=_this.isUseFenbi;
			json["useJifen"]=_this.isUseJifen;
			json["qianduanMoney"]=Number(qianduanMoney);
			json["payType"]=_this.payType;
			json["code"]=_this.tuijianCode;
			_this.isputIn=true;
			Lib.M.ajax({
				'url':memberAPI.activeAPI.buyDuofenCard_POST,
                'type': 'POST',
                'data':{
                   "json":json
                },
                'success':function (data){
                	_this.isputIn=false;
                	if(data.code==0){
                		_this.$router.push({ name: 'mycoupon',params:{busId:_this.busId}});
                	}
                	console.log(data);
                },
                'warning':function(err){
                	_this.isputIn=false;
                	if(err.code==6009){
                		window.location.href=err.msg
                	}else{
                		Lib.M.tipsMsg(err.msg);
                	}
                },
                'error':function(err){
                	_this.isputIn=false;
                	Lib.M.tipsMsg(err.msg);
                }

            })
		},
		chooseBuyWay(){
			var _this=this;
			_this.ischoosebuy=true;
		},
		closeBuyWin(){
			var _this=this;
			_this.ischoosebuy=false;
		},
		changeBuyWay(pos){
			var _this=this;
			_this.payWayName=_this.payTypes[pos].name;
            _this.payType=_this.payTypes[pos].payType;
            _this.ischoosebuy=false;
		}
	},
	created:function(){
		var _this=this;
        _this.loadData();
    },
	beforeCreate () {
	    window.document.querySelector('body').setAttribute('style', 'background:#f3f3f3')
	},
	beforeDestroy () {
	    // window.document.querySelector('body').setAttribute('style', '')
	}
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
	
</style>



