<template>
	<div class="writeoffSucc">
		<div class="succeed">
			<p v-if="issucce"><i class="icon iconfont icon-chenggong1"></i></p>
			<p v-if="isfail">  <i class="icon iconfont icon-cuowu"></i></p>
			<p  class="issucce">{{cardmsg}}</p>
			<p class="iszhekou">{{cardTitle}}</p>
			<p>{{cardCode}}</p>
		</div>
	</div>
</template>

<script>
import CouponFooter from '@/components/CouponFooter'
import Lib from '@/assets/js/Lib';
export default {
	data () {
	    return {
	    	isfail:false,
	    	issucce:false,
	    	isnoGet:false,
	    	busId:0,
	     	code:0,
	    	url:'',
	    	cardmsg:'',
	    	cardCode:'',
	    	cardTitle:'',
	    }
	},
  	components:{
	    CouponFooter
	},
	methods:{
		loadData(){
			var _this=this,json={};
			_this.busId= _this.$route.params.busId;
			_this.code= _this.$route.params.code;
			_this.url=window.location.href;
			json["busId"]=_this.busId;
			json["requestUrl"]=_this.url;
			json["code"]=_this.code;
			Lib.M.ajax({
				'url':memberAPI.activeAPI.useVerificationByUser_POST,
                'type': 'POST',
                'data':{
                   "json":json
                },
                'success':function (data){
                	if(data.code==0){
                		_this.cardmsg='核销成功';
                		_this.cardCode=data.data.duofencardCode;
                		_this.cardTitle=data.data.title;
                		console.log(data)
                		_this.issucce=true;
                	}
                	
                },
                'warning':function(err){
                	_this.isfail=true;
                	_this.cardmsg=err.msg;
                },
                'error':function(err){
                	Lib.M.tipsMsg('请求数据失败');
                }

            })
		},
	},
	created:function(){
		var _this=this;
        _this.loadData();
    },
	beforeCreate () {
	    window.document.querySelector('body').setAttribute('style', 'background:#fa5454')
	},
	beforeDestroy () {
	    // window.document.querySelector('body').setAttribute('style', '')
	}
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>

</style>



