import conf from './conf'
import axios from 'axios'
import { Toast } from 'mint-ui';

var oproto = Object.prototype;
var serialize = oproto.toString;
var Rxports = {
  /**
   * 封装axios，减少学习成本，参数基本跟jq ajax一致
   * @param {String} type			请求的类型，默认post
   * @param {String} url				请求地址
   * @param {String} time			超时时间
   * @param {Object} data			请求参数
   * @param {String} dataType		预期服务器返回的数据类型，xml html json ...
   * @param {Object} headers			自定义请求headers
   * @param {Function} success		请求成功后，这里会有两个参数,服务器返回数据，返回状态，[data, res]
   * @param {Function} error		发送请求前
   * @param return 
   */
  ajax: function(opt) {

    var opts = opt || {};

    if (!opts.url) {
      alert('请填写接口地址');
      return false;
    }

    axios({
      method: opts.type || 'POST',
      url: opts.url,
      params: opts.data || {},
      header: opts.header || {
        //'Content-Type':'application/x-www-form-urlencoded'
        'Content-Type': 'application/x-www-form-urlencoded;charset=utf-8'
      },
      // `baseURL` 将自动加在 `url` 前面，除非 `url` 是一个绝对 URL。
      // 它可以通过设置一个 `baseURL` 便于为 axios 实例的方法传递相对 URL
      //baseURL:'http://member.yifriend.net/',
      baseURL: memberAPI.activeAPI.homeUrl,

      // baseURL:'http://113.106.202.53:13887',
      timeout: opts.time || 10 * 10000,
      responseType: opts.dataType || 'json'
    }).then(function(res) {
      if (res.status == 200) {
        if (res.data.code == 0) {
          if (opts.success) {
            opts.success(res.data, res);
          }
        }
        if (res.data.code == 1) {
          if (opts.error) {
            opts.error(res.data, res);
          }
        }
        if (res.data.code > 1) {
          switch (res.data.code) {
            case 4004:
              opts.warning(res.data, res);
              break;
            case 4006:
              window.location.href=res.data.data;
              break;
            case 5017:
              window.location.href = res.data.msg;
              break;
            case 5025:
              break;
            default:
              opts.warning(res.data, res);
          }
        }
      } else {
        if (data.error) {
          opts.error(error);
        } else {
          alert('好多人在访问呀，请重新试试[timeout]');
        }

      }
    }).catch(function(error) {
      console.log(error);
      if (opts.error) {
        opts.error(error);
      } else {
        console.log("好多人在访问呀，请重新试试[timeout]")
      }
    });

  },
  tipsMsg(msg,time,style){
	var _this=this;
	var time=time||1500;
  var style=style||'';
	Toast({
    	message:msg,
    	duration:time,
      className:style
    });
},
//检测必填项是否为空
  checkPickClub(opts){
    var isTrue=[],isFalse=[],count = 0;
    for(let i in opts){
      count++;
      if(opts[i]==false){
        isFalse.push(opts[i]);
      }
      if(opts[i]==true){
        isTrue.push(opts[i]);
      }
    }
    if(count==isTrue.length){
      return true;
    }else{
      return false;
    }
  },
  // 周末转换
  weekSwitch(val,useTime){
    if(val!=null){
      if(useTime==1){
        if(val.week!=null){
          val.week=val.week.split(',');
          var arr = ["周一","周二","周三","周四","周五","周六","周日"];
          for(let i=0;i<val.week.length;i++){
            if(val.week[i]==0){
              val.week[i]=7;
            }
            val.week[i]=arr[val.week[i]-1];
          }
          val.week=val.week.join('、')
        }

        if(val.otherTime==1){
          val.otherTimeSets='';
          var hdz=JSON.parse(val.otherTimeSet)
          for(let j=0;j<hdz.length;j++){
              val.otherTimeSets+=`${hdz[j].month_start}-${hdz[j].day_start}至${hdz[j].month_end}-${hdz[j].day_end}不可用</br>`;
          }
          console.log( val)
        }
        return val;
      }
    }
  },
  //判断数字是否有小数
  isDot(num) {
    var nowNum=num.toString(),result =nowNum.indexOf("."),resultMoney=[];
    if(result != -1) {
        resultMoney=nowNum.split('.');
        if(resultMoney[1].length==1){
          resultMoney[1]=resultMoney[1]+'0';
        }
       return resultMoney;
    } else {
      resultMoney.push(num,'00');
       return resultMoney;
    }
  },
  //时间戳转换
  formatTime: function(date, val) {
    var date = new Date(date);
    var year = date.getFullYear()
    var month = date.getMonth() + 1
    var day = date.getDate()

    var hour = date.getHours()
    var minute = date.getMinutes()
    var second = date.getSeconds()

    if (val) {
      return [year, month, day].map(this.formatNumber).join('-');
    } else {
      return [year, month, day].map(this.formatNumber).join('-') + ' ' + [hour, minute, second].map(this.formatNumber).join(':');
    }
  },
  //手机号码验证
  checkPhone: function(phone) {
    if (!(/^1[34578]\d{9}$/.test(phone))) {
      return true;
    } else {
      return false;
    }
  },
  //检测数字
  checkNumber: function(number) {
    if (!(/^\+?[1-9][0-9]*$/.test(number))) {
      return true;
    } else {
      return false;
    }
  },
  // 检测邮箱
  checkEmail: function(email) {
    if (!(/^([a-zA-Z0-9]+[_|\_|\.]?)*[a-zA-Z0-9]+@([a-zA-Z0-9]+[_|\_|\.]?)*[a-zA-Z0-9]+\.[a-zA-Z]{2,3}$/.test(email))) {
      return true;
    } else {
      return false;
    }
  },
  //身份证验证
  checkidCard(card) {
    var code = card.toUpperCase();
    var city = {
      11: "北京",
      12: "天津",
      13: "河北",
      14: "山西",
      15: "内蒙古",
      21: "辽宁",
      22: "吉林",
      23: "黑龙江 ",
      31: "上海",
      32: "江苏",
      33: "浙江",
      34: "安徽",
      35: "福建",
      36: "江西",
      37: "山东",
      41: "河南",
      42: "湖北 ",
      43: "湖南",
      44: "广东",
      45: "广西",
      46: "海南",
      50: "重庆",
      51: "四川",
      52: "贵州",
      53: "云南",
      54: "西藏 ",
      61: "陕西",
      62: "甘肃",
      63: "青海",
      64: "宁夏",
      65: "新疆",
      71: "台湾",
      81: "香港",
      82: "澳门",
      91: "国外 "
    };
    var tip = "";
    var pass = true;

    if (!code ||
      !/^[1-9][0-9]{5}(19[0-9]{2}|200[0-9]|2010)(0[1-9]|1[0-2])(0[1-9]|[12][0-9]|3[01])[0-9]{3}[0-9xX]$/i
      .test(code)) {
      tip = "身份证号格式错误";
      pass = false;
    } else if (!city[code.substr(0, 2)]) {
      tip = "地址编码错误";
      pass = false;
    } else {
      //18位身份证需要验证最后一位校验位
      if (code.length == 18) {
        code = code.split('');
        //∑(ai×Wi)(mod 11)
        //加权因子
        var factor = [7, 9, 10, 5, 8, 4, 2, 1, 6, 3, 7, 9, 10, 5, 8,
          4, 2
        ];
        //校验位
        var parity = [1, 0, 'X', 9, 8, 7, 6, 5, 4, 3, 2];
        var sum = 0;
        var ai = 0;
        var wi = 0;
        for (var i = 0; i < 17; i++) {
          ai = code[i];
          wi = factor[i];
          sum += ai * wi;
        }
        var last = parity[sum % 11];
        if (parity[sum % 11] != code[17]) {
          //tip = "校验位错误";
          pass = false;
        }
      }
    }
    if (!pass) {
      return false;
    } else {
      return true;
    }
  },
  //名字验证
  checkName: function(name) {
    if (!(/^([\u4e00-\u9fa5]{1,20}|[a-zA-Z\.\s]{1,20})$/.test(name))) {
      return true;
    } else {
      return false;
    }
  },
  //数字加0转换
  formatNumber: function(n) {
    n = n.toString()
    return n[1] ? n : '0' + n
  },
  // 判断密码
  checkPassWord(password) {
    if (!(/^(?![0-9]+$)(?![a-zA-Z]+$)[0-9A-Za-z]{6,15}$/.test(password))) {
      return true;
    } else {
      return false;
    }
  },
  /*判定是否类数组，如节点集合，纯数组，arguments与拥有非负整数的length属性的纯JS对象*/
  isArrayLike: function(obj) {
    if (!obj)
      return false
    var n = obj.length
    if (n === (n >>> 0)) { //检测length属性是否为非负整数
      var type = serialize.call(obj).slice(8, -1)
      if (/(?:regexp|string|function|window|global)$/i.test(type))
        return false
      if (type === "Array")
        return true
      try {
        if ({}.propertyIsEnumerable.call(obj, "length") === false) { //如果是原生对象
          return /^\s?function/.test(obj.item || obj.callee)
        }
        return true
      } catch (e) { //IE的NodeList直接抛错
        return !obj.window //IE6-8 window
      }
    }
    return false
  },
  /*遍历数组与对象,回调的第一个参数为索引或键名,第二个或元素或键值*/
  each: function(obj, fn) {
    var That = this;
    if (obj) { //排除null, undefined
      var i = 0
      if (That.isArrayLike(obj)) {
        for (var n = obj.length; i < n; i++) {
          if (fn(i, obj[i]) === false)
            break
        }
      } else {
        for (i in obj) {
          if (obj.hasOwnProperty(i) && fn(i, obj[i]) === false) {
            break
          }
        }
      }
    }
  },
  /**
   * 获取url传过来的参数
   * @param name 	获取的参数
   * @param Url 		自定义获取参数的链接
   * @param return
   */
  getUrlQuery: function(name, Url) {

    //URL GET 获取值
    　　
    var reg = new RegExp("(^|\\?|&)" + name + "=([^&]*)(\\s|&|$)", "i"),
      url = Url || location.href;　　
    if (reg.test(url))　　 return unescape(RegExp.$2.replace(/\+/g, " "));　　
    return "";

  },

  /**
   * 深复制
   * @param src
   * @returns {{}}
   */
  copy(obj) {
    var dst = obj.constructor === Array ? [] : {};
    for (var prop in obj) {
      if (obj.hasOwnProperty(prop)) {
        if (typeof obj[prop] == 'object') {
          dst[prop] = this.copy(obj[prop])
        } else {
          dst[prop] = obj[prop];
        }
      }
    }
    return dst;
  }


};
export default Rxports;



