<template>
	<div class="recommend">
		<div class="useCImg">
   			<img :src="imgPath" v-show="isImg">
   			<span class="circleLeft" :style="'background:'+bgColor"></span>
	   		<span class="circleRight" :style="'background:'+bgColor"></span>
   		</div>
   		<div>
   			<div class="qrCode">
   				<div  v-if="tuijianIden==0">
	    			<p class="qrCodeP1">{{duofenCardNewData.title}}</p>
	    			<p class="qrCodeP3 qrCodeP3P">扫码领券</p>
   				</div>
   				<div v-if="tuijianIden==1" style="margin: 0.4rem 0">
   					<p class="qrCodeP3 qrCodeP3Pfirst" >{{tuijianCon}}</p>
   				</div>
    			<img class="qrCodeImg" :src="urlCardNo" v-show="isImg" style="margin-top: 0.2rem">
    			<p class="qrCodeP2" v-if="tuijianIden==0">转发此页面并推荐给好友用券，还能获取奖励哦~</p>
    			<p class="qrCodeP2" v-if="tuijianIden==1">识别图中二维码，领取优惠券</p>
    		</div>
   		</div>
	</div>
</template>

<script>
import CouponFooter from '@/components/CouponFooter'
import Lib from '@/assets/js/Lib';
export default {
	data () {
	    return {
	    	isfail:false,
	    	issucce:true,
	    	isnoGet:false,
	    	receiveId:0,
	    	imgPath:'',
	    	duofenCardNewData:[],
	    	wxshareData:[],
	    	tuijianCode:'',
	    	urlCardNo:'',
	    	isImg:false,
	    	bgColor:'#764ba5',
	    	tuijianIden:0,
	    	tuijianCon:'',
	    	share:{
	    		urlShare:'',
		    	shareTitle:'您的好友给您推荐一张优惠券，赶紧领取吧!',
		    	shareDesc:'识别图中二维码领取吧!',
		    	shareImg:'http://wx.qlogo.cn/mmopen/ib5Ef9ImF1HN9hPsiaBlibGe5iaQDGFtJ4J33IicwtlB8YxSWzfLrmBNqjsdqOUM3MFtJpuZRdpNhwIqguw0D5ibOhxaFKBlsP20De/0'
	    	}
	    }
	},
  	components:{
	    CouponFooter
	},
	methods:{
		loadData(){
			var _this=this,json={};
			_this.busId= _this.$route.params.busId;
			_this.receiveId= _this.$route.query.receiveId;
			_this.url=window.location.href;
			json["busId"]=_this.busId;
			json["requestUrl"]=_this.url;
			json["receiveId"]=_this.receiveId;
			json["wxshareUrl"]=_this.url;
			_this.share.urlShare=_this.url;
			Lib.M.ajax({
				'url':memberAPI.activeAPI.tuijianDuofenCard_POST,
                'type': 'POST',
                'data':{
                   "json":json
                },
                'success':function (data){
                	if(data.code==0){
                		_this.duofenCardNewData=data.data.duofenCardNew;
                		_this.imgPath=data.data.path+_this.duofenCardNewData.iconUrlList;
                		_this.tuijianCode=data.data.code;
                		_this.bgColor=_this.duofenCardNewData.color;
                		_this.tuijianIden=data.data.tuijianIden;
                		_this.share.shareTitle=`您的好友给您推荐一张${_this.duofenCardNewData.title},赶紧领取吧!`;
                		_this.tuijianCon=`您的好友给您推荐一张${_this.duofenCardNewData.title}`;
                		window.document.querySelector('body').setAttribute('style', 'background:'+_this.bgColor);
                		_this.urlCardNo=memberAPI.activeAPI.homeUrl+memberAPI.activeAPI.tuijianQRcode_GET+"?busId="+_this.busId+"&code="+_this.tuijianCode;
                		Lib.M.ajax({
					      	'url': memberAPI.activeAPI.wxshareCard_POST,
					      	'type': 'POST',
					      	'data': {
					        	"json": json
					      	},
		      				'success': function(data) {
		      					_this.wxshareData=data.data;
		      				},
		      				'error':function(data){
		      				}
		      			})
                		_this.isImg=true;
                	}
                	console.log('推荐',data)
                },
                'warning':function(err){
                	Lib.M.tipsMsg(err.msg);
                },
                'error':function(err){
                	Lib.M.tipsMsg('请求数据失败');
                }

            })
		},
	},
	created:function(){
		var isfirst=window.localStorage.getItem('first');
			if(isfirst==null){
				isfirst=0;
			}
	    	if(isfirst==0){
	    		location.reload()
	    		window.localStorage.setItem('first',1)
	    	}
		var _this=this;
        _this.loadData();
    },
    watch:{
		wxshareData(newValue, oldValue){
			var _this=this;
			wx.config({
	            debug: false, // 开启调试模式,调用的所有api的返回值会在客户端alert出来，若要查看传入的参数，可以在pc端打开，参数信息会通过log打出，仅在pc端时才会打印。
	            appId: newValue.appid, // 必填，公众号的唯一标识
	            timestamp: newValue.timestamp, // 必填，生成签名的时间戳
	            nonceStr: newValue.nonce_str, // 必填，生成签名的随机串
	            signature: newValue.signature, // 必填，签名，见附录1
	            jsApiList: ["onMenuShareAppMessage", 'onMenuShareTimeline'] // 必填，需要使用的JS接口列表，所有JS接口列表见附录2
	        });
			wx.ready(function() {
	            //分享给朋友
	           
	            wx.onMenuShareAppMessage({
		            title: _this.share.shareTitle, // 分享标题
		            desc: _this.share.shareDesc, // 分享描述
		            link: _this.share.urlShare, // 分享链接
		            imgUrl: _this.share.shareImg, // 分享图标
		            success: function() {
		                // 用户确认分享后执行的回调函数
		                Lib.M.tipsMsg('分享成功')
		            },
		            cancel: function() {
		                // 用户取消分享后执行的回调函数
		                 Lib.M.tipsMsg('取消分享')
		            },
		            error: function(res) {
		            }
	            });
	            //分享到朋友圈
	            wx.onMenuShareTimeline({
	             	title: _this.share.shareTitle, // 分享标题
	             	desc: _this.share.shareDesc, // 分享描述
	              	link: _this.share.urlShare, // 分享链接
	              	imgUrl: _this.share.shareImg, // 分享图标
	              	success: function() {
	              		 Lib.M.tipsMsg('分享成功')
	                // 用户确认分享后执行的回调函数
	              	},
	              	cancel: function() {
	              		Lib.M.tipsMsg('取消分享')
	                // 用户取消分享后执行的回调函数
	              	}
	            });
	        });
		}
	},
	beforeCreate () {
	    window.document.querySelector('body').setAttribute('style', 'background:#764ba5')
	},
	destroyed(){
		window.localStorage.setItem('first',0)
	},
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>

</style>



