<template>
	<div class="showDetails">
		<div class="showDetailsCom" v-if="">
			<div class="showDetailsImg">
				<span><i class="icon iconfont icon-gou"></i></span>
				<p>提现成功</p>
			</div>
			<div class="details">
				<p class="clearfix"><span class="fl">提现时间</span><span class="fr">{{details.time}}</span></p>
				<!-- <p class="clearfix"><span class="fl">微信号</span><span class="fr">WeChat 0001</span></p> -->
				<p class="clearfix"><span class="fl">提现金额</span><span class="fr">¥{{details.money}}</span></p>
			</div>
			<div class="withdrawButton">
				<router-link tag="p" :to="{ name: 'home',params: { busId: busId }}" >
					<button class=" activedraw" type="button" name="" value="" >完成</button>
				</router-link>
			</div>
		</div>
	</div>
</template>
<script >
	import Lib from '@/assets/js/Lib';
	export default{
		data() {
		    return {
		    	busId:0,
		    	details:{
		    		money:0,
		    		time:''
		    	}
			}
		},
		components:{
		    // ClubFoot
		},
		methods:{
			loadData:function(){
				var _this=this,json={},date;
				date = new Date().getTime();
	            _this.details.time=Lib.M.formatTime(date);
				_this.busId= _this.$route.params.busId;
				_this.details= _this.$route.params.detail||_this.detail;
			},
		},
		created:function(){
	    	var _this=this;
	    	_this.loadData();  
	    }

	}
</script>
<style>
.showDetails .showDetailsCom{
	padding-top: 0.6rem;
}
.showDetails .showDetailsCom .showDetailsImg,.showDetails .showDetailsCom .withdrawButton p{
	font-size: 1rem;
}
.showDetails .showDetailsCom .showDetailsImg span{
	width: 1.6rem;
	height: 1.6rem;
	line-height: 1.6rem;
	background: #29cc6d;
	border-radius: 0.8rem;
	font-size: 1.6rem
}
.showDetails .showDetailsCom .showDetailsImg span .icon{
	font-size: 0.9rem;
	line-height: 1.6rem;
	height: 1.6rem;
	color: #fff;
	padding-left: 0.1rem;
	vertical-align: middle;
}
.showDetails .showDetailsCom .showDetailsImg p{
	margin: 0.45rem 0 1rem 0;
	font-size: 0.34rem;
}
.showDetails .showDetailsCom  .details{
	margin: 0 0.45rem 0.4rem 0.45rem;
	text-align: left;
}
.showDetails .showDetailsCom  .details p{
	margin-top: 0.3rem;
}
.showDetails .showDetailsCom  .details p span{
	font-size: 0.28rem;
}
.showDetails .showDetailsCom  .details p:last-child{
	padding-bottom: 0.5rem;
}
.showDetails .showDetailsCom  .details p span:first-child{
	color: #999;
}
.showDetails .showDetailsCom  .details p span:last-child{
	color: #333743;
}
</style>


