<template>
	<div class="AreaPop">
		<div  class="AreaPopCon">
			<div class="title">
				<input type="text" v-model="areaInput" @keyup="searchArea" name="" placeholder="搜索国家/地区">
				<span @click.stop="closeArea"><i class="icon iconfont icon-cha"></i></span>
			</div>
			<div class="meun">
				<ul>
					<li v-for="(item,index) in areaCodeData" @click="chooseAreaCode(index)">
						<span >{{item.country}}({{item.language}})</span>
						<span>+{{item.areacode}}</span>
					</li>
				</ul>
			</div>
		</div>
	</div>
</template>
<script>
import Lib from '@/assets/js/Lib';
	export default{
		data() {
		    return {
			    spanClose:true,
			    areaInput:'',
			    areaData:{
			    	currAreaCode:'+86',
			    	currId:1
			    },
			    totalData:[
			    	{
			    		country:'中国大陆',
			    		areacode:'86',
			    		language:'汉语'
			    	},
			    	{
			    		country:'香港',
			    		areacode:'852',
			    		language:'汉语'
			    	},
			    	{
			    		country:'澳门',
			    		areacode:'853',
			    		language:'汉语'
			    	}
			    ],
			    areaCodeData:[
			    	{
			    		country:'中国大陆',
			    		areacode:'86',
			    		language:'汉语'
			    	},
			    	{
			    		country:'香港',
			    		areacode:'852',
			    		language:'汉语'
			    	},
			    	{
			    		country:'澳门',
			    		areacode:'853',
			    		language:'汉语'
			    	}
			    ]

			}
		},
		// props:['titleCom','titleComTow','icon']
		props:{
			titleComTow:'',
			icon:{
				default:1
			}
		},
		 methods: {
		 	loadData:function(){
				var _this=this,json={};
				Lib.M.ajax({
			      	'url': memberAPI.activeAPI.findAreaPhone_POST,
			      	'type': 'POST',
			      	'data':json,
      				'success': function(data) {
      					_this.areaCodeData=data.data;
      					_this.totalData=data.data;
      					console.log('findAreaPhone',data)
      				}
      			})
				
			},
			searchArea(){
				var _this=this,value;
				value=_this.areaInput;
				if(value==''){
					_this.areaCodeData=_this.totalData;
					return;
				}
				for(let i=0;i<_this.totalData.length;i++){
					if(_this.totalData[i].country==value){
						_this.areaCodeData=[];
						_this.areaCodeData.push(_this.totalData[i]);
					}
				}
			},
            closeArea(){
            	var _this=this;
            	_this.areaInput='';
            	_this.areaCodeData=_this.totalData;
		        _this.$emit('close-areaCode',_this.spanClose);
		    },
		    chooseAreaCode(ind){
		    	var _this=this;
		    	_this.areaInput='';
		    	_this.areaData.currAreaCode=_this.areaCodeData[ind].areacode;
		    	_this.areaData.currId=_this.areaCodeData[ind].id;
		    	_this.areaCodeData=_this.totalData;
		    	_this.$emit('curr-areaCode',_this.areaData);
            	console.log(ind)
		    }
        },
        created:function(){
	    	var _this=this;
	    	_this.loadData();
	    },
	}
</script>
<style scope>
	
</style>


