<template>
	<div class="applystore">
		<div class="applystoretitle">
			<span>共{{shopData.length}}家适用门店</span>
			<span class="fr clearfix" @click.stop="closeApply"><i class="icon iconfont icon-cha"></i></span>
		</div>
		<div class="applystoreContent clearfix">
			<div class="applystoreP1 clearfix" @click.stop="chooseApply(index)" v-for="(item,index) in shopData">
				<div class="fl w75 applystorelf">
					<p>{{item.businessName}}</p>
					<p>{{item.address}}</p>
				</div>
				<div class="fr w23 applystorerg">
					<p v-if="item.distance>1000"> <i class="icon iconfont icon-left_atn"></i><span>{{Math.floor((item.distance/1000)* 100) / 100}}KM</span></p>
					<p v-else> <i class="icon iconfont icon-left_atn"></i><span>{{Math.floor(item.distance* 100) / 100}}m</span></p>
				</div>
			</div>
		</div>
		<div class="nostore">没有过多了</div>
		
	</div>
</template>

<script>
import Lib from '@/assets/js/Lib';
export default {
	data () {
		return {
	    	applyClose:1,
	    	otherPos:2,
	    	busId:0,
	    	url:'',
	    	cardId:0,
	    	latitude:'',//纬度
			longitude:'',//经度
			shopData:[],
	    }
	},
  	components:{

	},
	props: ['otherApply'],
	methods:{
		loadData(){
			var _this=this,json={},mapObj,geolocation;
			_this.busId= _this.$route.params.busId;
			_this.cardId= _this.$route.query.cardId;
			_this.url=window.location.href;
			json["busId"]=_this.busId;
			json["requestUrl"]=_this.url;
			json["receiveId"]=_this.cardId;
			mapObj = new AMap.Map('iCenter');
			mapObj.plugin('AMap.Geolocation', function () {
			    geolocation = new AMap.Geolocation({
			    	enableHighAccuracy: true,//是否使用高精度定位，默认:true
		            timeout:0          //超过10秒后停止定位，默认：无穷大
			    });
			    geolocation.getCurrentPosition();
			    AMap.event.addListener(geolocation, 'complete', function(data){
				    _this.longitude=data.position.getLng()
				    _this.latitude=data.position.getLat();
					json["longitude"]=_this.longitude;
					json["latitude"]=_this.latitude;
					console.log("json",json)
					if(_this.latitude!=''||_this.longitude!=undefined){
					   	Lib.M.ajax({
							'url':memberAPI.activeAPI.findShopByReceiveId_POST,
			                'type': 'POST',
			                'data':{
			                   "json":json
			                },
			                'success':function (data){
			                	if(data.code==0){
			                		_this.shopData=data.data;

			                	}
			                	console.log("门店",data)
			                	
			                },
			                'warning':function(err){
			                	Lib.M.tipsMsg(err.msg);
			                },
			                'error':function(err){
			                	Lib.M.tipsMsg('请求数据失败');
			                }

			            })					
					}
		       });//返回定位信息
			    AMap.event.addListener(geolocation, 'error', function(data){
			    	// Lib.M.tipsMsg(data.message);
			    });
			});
		},
		closeApply(){
        	var _this=this;
        	if(_this.otherApply){
        		_this.$emit('close-applyStore',_this.otherPos);
        	}else{
	       	 	_this.$emit('close-applyStore',_this.applyClose);
        	}
	    },
	    chooseApply(pos){
	    	var _this=this,shopLng,shopLat;
        	if(_this.otherApply){
        		let businessName=_this.shopData[pos].businessName;
        		let shopId=_this.shopData[pos].id;
        		_this.$emit('choose-Store',[_this.otherPos,businessName,shopId]);//核销
        	}else{
        		shopLat=Number(_this.shopData[pos].latitude);
				shopLng=Number(_this.shopData[pos].longitude);
	       	 	_this.$emit('choose-Store',[_this.applyClose]);//购买
	       	 	window.location="https://uri.amap.com/marker?position="+shopLng+","+shopLat;
        	}
	    }

	},
	created () {
		var _this=this;
		_this.loadData();
	    // window.document.querySelector('body').setAttribute('style', 'background:#f3f3f3')
	},
	beforeDestroy () {
	    // document.querySelector('body').setAttribute('style', '')
	}
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
.applystorerg .icon{
	font-size: 0.22rem;
	padding-right: 0.05rem;
}

</style>



