<template>
	<div class="myhome">
	   
	    <div class="component">
	    	<div v-if="hasMessage">
		    	<div class="category clearfix">
		    		<ul
					 v-infinite-scroll="loadMore"
					 infinite-scroll-disabled="loading"
					 infinite-scroll-distance="3">
					 	<li v-for="(item,index) in allCardData">
				    		<div class="categoryImg clearfix" 
				    		:class="[(item.cardBgState==0)?'tongyong':((item.cardBgState==1)?'yilingqu':((item.cardBgState==2)?'tongyqianw':''))]">
				    			<div class="categoryLeft  fl" :class="[(item.cardType==1)?'zhekouname':'']">
				    				<p v-if="item.cardType==2">¥<span class="free">{{item.cardName}}</span></p>
				    				<p v-else>{{item.cardName}}</p>
				    			</div>
				    			<div class="categoryRight buy fl " :class="[(item.gift==null)?'onlyTime':'']">
				    				<p class="title">{{item.title}}</p>
				    				<p class="buydetail" v-if="item.cardType==2"><span v-if="item.cashLeastCost>0">消费满{{item.cashLeastCost}}可用</span><span></span></p>
				    				<p class="buydetail" v-if="item.gift!=null">{{item.gift}}</p>
				    				<p class="time">有效期：{{item.beginTimestamp}}~{{item.endTimestamp}}</p>
				    			</div>
				    			<div v-if="(item.isBuy==1?true:false)">
					    			<div class="buyBot fl" @click="buyCoupon(index)" v-if="item.limitType==0&&item.lingquOver==0">
					    				<p class="buytitle">购买</p>
					    				<p class="buyprice">¥{{item.buyMoney}}</p>
					    			</div>
				    			</div>
				    			<div v-else>
					    			<div class="buyBot  fl" @click="buyCoupon(index)" v-if="item.limitType==0&&item.lingquOver==0">
					    				<p class="buytitle lingqu">领取</p>
					    			</div>
				    			</div>
				    		</div>
					 	</li>
			    	</ul>
			    	<p class="tipMsg" :class="{msgType:loading}" v-show="loading">{{jiazhai}}</p>
		    	</div>
	    	</div>
	    	<div class="nomessage" v-if="isnoLogin">
	    		<p>您还未登录，暂时无法查看更多优惠券信息</p>
	    	</div>
	    </div>
	    <div class="popupWin" v-show="isdetial">
	    	<div class="popupWinCo">
	    		<div class="close" @click="closeWin">
	    			<p> <i class="icon iconfont icon-guanbi"></i></p>
	    		</div>
	    		<div class="content clearfix">
	    			<div class="conTitle">
	    				<p class="totprice" v-if="curCardData.cardType==1">{{curCardData.discount}}折</p>
	    				<p class="totprice" v-if="curCardData.cardType==2">¥{{curCardData.reduceCost}}</p>
	    				<p class="totprice" v-if="curCardData.cardType==3">礼品券</p>
	    				<p class="totprice" v-if="curCardData.cardType==4">优惠券</p>
	    				<p class="discount">{{curCardData.title}}</p>
	    				<p class="discount" v-if="curCardData.cardType==2">满{{curCardData.cashLeastCost}}减{{curCardData.reduceCost}}</p>
	    				<span class="circleLeft"></span>
	   					<span class="circleRight"></span>
	    			</div>
	    			<div class="conDetial">
	    				<ul class="conDetialul" v-if="duofenCardTimeData==null">
	    					<li>有效期：{{curCardData.beginTimestamp}}~{{curCardData.endTimestamp}}</li>
	    					<li v-if="curCardData.useType==0">全场通用</li>
	    					<li v-if="curCardData.useTime==0">全时段可用</li>
	    					<li v-if="curCardData.gift!=null">{{curCardData.gift}}</li>
	    				</ul>
	    				<ul class="conDetialul" v-else>
	    					<li>有效期：{{curCardData.beginTimestamp}}~{{curCardData.endTimestamp}}</li>
	    					<li v-if="curCardData.useType==0">全场通用</li>
	    					<li v-if="curCardData.useTime==0">全时段可用</li>
	    					<li v-if="curCardData.useTime==1"> {{duofenCardTimeData.week}}可用</li>
	    					<li v-if="duofenCardTimeData.holidays==1">法定节假日不可用</li>
	    					<li v-if="duofenCardTimeData.otherTime==1" v-html="duofenCardTimeData.otherTimeSets"></li>
	    					<li v-if="curCardData.gift!=null">{{curCardData.gift}}</li>
	    				</ul>
	    				<p class="detial" @click="detial">查看详细使用说明<span> <i class="icon iconfont icon-right_atn"></i></span></p>
	    			</div>
	    		</div>
	    		 <div class="loginbut">
			    	<input class="isactive" type="button" name="" @click="buyNow" :value='buttonValue'>
			    </div>
	    	</div>
	    </div>
	   <div class="heightline"></div>
		<CouponFooter v-on:go-whichRoute="goWhich" :nowPos="whichPos"></CouponFooter>
	</div>
</template>

<script>
import CouponFooter from '@/components/CouponFooter'
import ClubFoot from "@/components/ClubFoot";
import Lib from '@/assets/js/Lib';
export default {
	data () {
	    return {
	     	isnoLogin:false,
	     	ismember:false,
	     	hasMessage:true,
	     	isdetial:false,
	     	whichPos:1,
	     	busId:0,
	     	url:'',
	     	uclogin:0,
	     	page:1,
	     	cardId:0,
	     	DuofenCardData:[],
	     	allCardData:[],
	     	cardType:0,
	     	isbuyTrue:true,
	     	isBuy:0,
	     	loading:false,
	     	jiazhai:'>>加载中<<',
	     	curCardData:[],
	     	duofenCardTimeData:[],
	     	cardState:0,
	     	buttonValue:'立即购买',
	    }
	},
  	components:{
	    CouponFooter
	},
	methods:{
		loadData(){
			var _this=this,json={};
			_this.busId= _this.$route.params.busId;
			_this.tuijianCode= _this.$route.params.tuijianCode;
			window.localStorage.setItem('first',0)
			window.localStorage.setItem('share',0)
			if(_this.tuijianCode==undefined){
				_this.tuijianCode=0;
			}
			_this.url=window.location.href;
			json["busId"]=_this.busId;
			json["requestUrl"]=_this.url;
			json["uclogin"]=_this.uclogin;
			json["page"]=_this.page;
			json["code"]=_this.tuijianCode;
			_this.getData(json);
		},
		getData(data){
			var _this=this, date = new Date().getTime();
			Lib.M.ajax({
				'url':memberAPI.activeAPI.findPublishDuofenCard_POST,
                'type': 'POST',
                'data':{
                   "json":data
                },
                'success':function (data){
                		// _this.hasMessage = true;
                	if(data.code==0){
                		_this.loading = false;
                		_this.DuofenCardData=data.data;
                		if(_this.DuofenCardData.length!=0||_this.DuofenCardData.length!=null){

	                		for(let i=0;i<_this.DuofenCardData.length;i++){
	                			_this.cardType=_this.DuofenCardData[i].cardType;
	                			if(_this.DuofenCardData[i].useType==0){
	                				_this.DuofenCardData[i].cardBgState=0;
	                			}
	                			if(_this.DuofenCardData[i].limitType==1){
	                				_this.DuofenCardData[i].cardBgState=1;
	                			}
	                			if(_this.DuofenCardData[i].limitType==0){
	                				if(_this.DuofenCardData[i].lingquOver==1){
		                				_this.DuofenCardData[i].cardBgState=2;
		                			}
		                			if(_this.DuofenCardData[i].lingquOver==0){
		                				_this.DuofenCardData[i].cardBgState=0;
		                			}
	                			}
	                			switch (_this.cardType){ 
									case 1 : //折扣券
										_this.DuofenCardData[i].cardName=_this.DuofenCardData[i].discount+'折';
										break; 
									case 2 ://代金券
										_this.DuofenCardData[i].cardName=_this.DuofenCardData[i].reduceCost;

										break; 
									case 3: //礼品券
										_this.DuofenCardData[i].cardName='礼品券';
										break; 
									case 4://通用券
										_this.DuofenCardData[i].cardName='优惠券';
										break; 
									default :
										break; 
								}
								if(_this.DuofenCardData[i].type==1){
			                		_this.DuofenCardData[i].beginTimestamp=Lib.M.formatTime(date+_this.DuofenCardData[i].fixedBeginTerm*24*60*60*1000,true);
			                		_this.DuofenCardData[i].endTimestamp=Lib.M.formatTime(date+_this.DuofenCardData[i].fixedTerm*24*60*60*1000,true);
								}
	                			_this.allCardData.push(_this.DuofenCardData[i]);
	                		}
                		}
                		
                	}
                	console.log('first',data);
                	
                },
                'error':function(err){
                	Lib.M.tipsMsg('链接失败,请联系管理员!');
                	_this.hasMessage=false;
                }
			})
		},
		loadMore() {
			var _this=this,json={};
			if(_this.DuofenCardData.length==0||_this.DuofenCardData.length<5){
				_this.loading = false;
				if(_this.page==1){
					return
				}
				_this.jiazhai='已经到底啦~'
				return false
			}else{
				_this.loading = true;
				_this.jiazhai='>>数据获取中<<'
				_this.page+=1;
			}
			json["busId"]=_this.busId;
			json["requestUrl"]=_this.url;
			json["uclogin"]=_this.uclogin;
			json['page']=_this.page;
			_this.getData(json);

		},
		login(){
			var _this=this;
			_this.$router.push({ name: 'login'});
		},
		// 领取优惠券
		freeGet(cardId){
			var _this=this,json={},jsons={};
			json["busId"]=_this.busId;
			json["requestUrl"]=_this.url;
			json["cardId"]=cardId;
			json["code"]=_this.tuijianCode;

			jsons["busId"]=_this.busId;
			jsons["requestUrl"]=_this.url;
			jsons["uclogin"]=_this.uclogin;
			jsons["page"]=_this.page;
			Lib.M.ajax({
				'url':memberAPI.activeAPI.getDuofenCard_POST,
                'type': 'POST',
                'data':{
                   "json":json
                },
                'success':function (data){
                	if(data.code==0){
                		Lib.M.tipsMsg('领取成功');
                		_this.getData(jsons);
                	}
                },
                'warning':function(err){
                	if(err.code==5026){
                		_this.$router.push({ name: 'login',params:{busId:_this.busId},query:{cardId:_this.cardId,tuijianCode:_this.tuijianCode}});
                	}
                	if(err.code==6007){
                		_this.$router.push({ name: 'closeaccount',params:{busId:_this.busId},query:{cardId:_this.cardId,tuijianCode:_this.tuijianCode}});
                	}
                	// Lib.M.tipsMsg(err.msg);
                }

            })
		},
		buyCoupon(pos){
			var _this=this,json={};
			_this.cardId=_this.allCardData[pos].cardId;
			_this.cardId=_this.allCardData[pos].cardId;
			_this.isBuy=_this.allCardData[pos].isBuy;
			if(_this.isBuy==0){
				_this.freeGet(_this.cardId);
				return
			}
			_this.buttonValue='立即购买 ¥'+_this.allCardData[pos].buyMoney;
			json["busId"]=_this.busId;
			json["requestUrl"]=_this.url;
			json["uclogin"]=_this.uclogin;
			json["cardId"]=_this.cardId;
			Lib.M.ajax({
				'url':memberAPI.activeAPI.findDuofenCardDetails_POST,
                'type': 'POST',
                'data':{
                   "json":json
                },
                'success':function (data){
                	if(data.code==0){
                		_this.curCardData=data.data.duofenCardNew;
	                	_this.duofenCardTimeData=data.data.duofenCardTime;
                		_this.duofenCardTimeData=Lib.M.weekSwitch(_this.duofenCardTimeData,_this.curCardData.useTime);

                	}
                	
                	
                }
			})
			_this.isdetial=true
		},
		closeWin(){
			var _this=this;
			_this.isdetial=false
		},
		buyNow(){
			var _this=this,ccc;
			_this.freeGet(_this.cardId);
		},
		detial(){
			var _this=this;
			_this.$router.push({ name: 'homedetail',params:{busId:_this.busId},query:{cardId:_this.cardId}});
		},
		goWhich(pos){
			var _this=this;
			if(pos){
				_this.whichPos=pos;
			}
		}
	},
	created:function(){
		var _this=this;
        _this.loadData();
    },
	beforeCreate () {
	    window.document.querySelector('body').setAttribute('style', 'background:#f5f5f5')
	},
	beforeDestroy () {
	    // window.document.querySelector('body').setAttribute('style', '')
	}
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
.onlyTime{
	margin-top: 0.15rem;
}
.tipMsg{
	font-size: 0.28rem;
	color: #999;
	margin-top: 0.3rem;
}
</style>



