<template>
	<div class="withdraw">
		<div class="clearfix topuphead">
			<a @click="$router.go(-1)" class="ago fl"> <i class="icon iconfont icon-left_atn"></i></a>
		</div>
		<div class="withdrawCom" v-if="">
			<div class="withdrawComDetil">
				<!-- <p class="title">到账微信号<span>Wechat 0001</span></p> -->
				<div class="money">
					<span>提现金额</span>
					<p>
						<label>¥</label>
						<input type="number" name="" placeholder="请输入提取金额" v-model.number="money" @focus="inputClear" @keyup="inputValue">
					</p>
				</div>
				<p class="balances">可提现金额¥<span>{{totalMoney}}</span>，<span @click="withdrawMoney('all')">全部提现</span></p>
				<div class="withdrawButton">
					<button  :class="[isTrue?'activedraw':'buttonDisable']" @click="withdrawMoney" name="" value="" >提现</button>
				</div>
			</div>

		</div>
	</div>
</template>
<script >
	import Lib from '@/assets/js/Lib';
	import { Toast } from 'mint-ui';
	export default{
		data() {
		    return {
		    	busId:0,
		    	url:'',
		    	totalMoney:50,
		    	money:'',
		    	isTrue:false,
		    	istijiao:false,
			}
		},
		components:{
		},
		methods:{
			loadData:function(){
				var _this=this;
				_this.busId= _this.$route.params.busId;
				_this.totalMoney= _this.$route.query.tuijianMoney;
				_this.url=window.location.href;
				
			},
			inputValue(){
				var _this=this;
				if(_this.money<=_this.totalMoney){
					_this.isTrue=true;
				}
				if(_this.money>_this.totalMoney||_this.money==''||_this.money==0){
					_this.isTrue=false;
				}
				
			},
			inputClear(){
				var _this=this;
				_this.money='';
			},
			withdrawMoney(pos){
				var _this=this,json={};
				if(pos=='all'){
					_this.isTrue=true;
					_this.money=_this.totalMoney
					if(_this.money==0){
						_this.isTrue=false;
					}
					return false;
				}
				if(!_this.isTrue){
					Toast({
						message: '提现金额大于零小于等于可提现金额',
					  	duration:2000,
					  	className:'payStyle'
					})
					return false;
				}
				if(_this.istijiao){
					return false;
				}
				json["busId"]=_this.busId;
				json["requestUrl"]=_this.url;
				json["money"]=_this.money;
				_this.istijiao=true;
				Lib.M.ajax({
					'url':memberAPI.activeAPI.pickMoney_POST,
	                'type': 'POST',
	                'data':{
	                   "json":json
	                },
	                'success':function (data){
	                	_this.istijiao=false;
	                	if(data.code==0){
	                		let detail={},date;
	                		date = new Date().getTime();
	                		date=Lib.M.formatTime(date);
	                		detail['time']=date;
	                		detail['money']=_this.money;
	                		_this.$router.push({ name:'showdetails',params: {busId:_this.busId, detail: detail }})
	                	}
	                	console.log('pickMoney',data)
	                },
	                'error':function(err){
	                	_this.istijiao=false;
	                	Toast({
							message: "提现失败",
						  	duration: 2000
						})
	                }
				})
			}
		},
		created:function(){
	    	var _this=this;
	    	_this.loadData();
	    },
	    beforeCreate () {
		    window.document.querySelector('body').setAttribute('style', 'background:#fff')
		}
	}
</script>
<style>

</style>


