<template>
	<div class="myhome">
	    <div class="component">
	    	<div v-if="hasMessage">
		    	<div class="category clearfix">
		    		<div class="nomenberBuy">
		    			<p>您已购买<span class="nomenberBuyNumb">1</span>张优惠券</p>
		    		</div>
		    		<div class="categoryImg yigoumai clearfix">
		    			<div class="categoryLeft fl">
		    				<p>礼品券</p>
		    			</div>
		    			<div class="categoryRight nohuiyuanRight buy fl">
		    				<p class="title">天悦迷你月饼券</p>
		    				<p class="buydetail">可兑换天悦迷你月饼1盒、五仁月饼1盒、叉烧月饼1盒、水果月饼...</p>
		    				<p class="time">有效期：2017/10/1~2017/11/1</p>
		    			</div>
		    		</div>
		    		<!-- <div class="categoryImg yilingqu  clearfix">
		    			<div class="categoryLeft zhekouname fl">
		    				<p>9.5折</p>
		    			</div>
		    			<div class="categoryRight zhekou nohuiyuanRight buy fl">
		    				<p class="title">9.5折优惠券</p>
		    				<p class="buydetail">可兑换天悦迷你月饼1盒、五仁月饼1盒、叉烧月饼1盒、水果月饼...</p>
		    				<p class="time">有效期：2017/10/1~2017/11/1</p>
		    			</div>
		    		</div> -->
		    	</div>
		    	<!-- 公众号 -->
		    	<div class="applystoreP1 lookvipcn clearfix">
					<div class="fl w75 applystorelf">
						<p>查看公众号</p>
						<p>优惠券已存放至商家公众号的“我的优惠券”,关注公众号，使用优惠券更方便</p>
					</div>
					<div class="fr w20 applystorerg">
						<p @click="lookOver"> <i class="icon iconfont icon-right_atn"></i></p>
					</div>
				</div>
				<!--  -->
				 <div class="loginbut">
			    	<input class="isactive" type="button" name="" @click="immediate"  value="立即使用">
			    </div>
			    <div class="vipcnTip">
			    	<p>优惠券使用提示</p>
			    	<ul>
			    		<li>凭手机号直接使用该优惠券</li>
			    		<li>在“我的优惠券”查看优惠券详情</li>
			    	</ul>
			    </div>
	    	</div>
	    	<div class="nomessage" v-else>
	    		<p>您还未登录，暂时无法查看更多优惠券信息</p>
	    	</div>
	    </div>
	    	<!-- 购买弹窗 -->
	   	 <div class="overlayer" @touchmove.prevent v-show="isOverlayer">
		   	<div class="popupWin useWin" v-show="isUseWin">
		    	<div class="popupWinCo useWinCo">
		    		<div class="close clearfix">
		    			<p @click="cancel" class="vipcPayactive"> 取消</p>
		    			<div class='useCTitleOne'>
				   			<p class="useCTitleOnep1">多粉力量</p>
				   			<p class="useCTitleOnep2">9.5折优惠券</p>
				   			<span class="circleLeft"></span>
				   			<span class="circleRight"></span>
				   		</div>
		    		</div>
		    		<!-- 购买方式 -->
		    		<div v-show="!isSucceed">
			    		<div class="qrCode" v-show="isqrCode">
			    			<p class="qrCodeP1">请向店员出示以下优惠券二维码</p>
			    			<img class="qrCodeImg" src="/static/images/5.jpg">
			    			<p class="qrCodeP2">1522342256465422</p>
			    		</div>
			    		<div v-show="iswriteOff">
							<div class="menuP1 writeOffs clearfix">
								<span>选择你所在的门店</span>
								<span class="fr"  @click="applystore(1)"><i class="icon iconfont icon-right_atn"></i></span>
							</div>
							<div class="useCContent writeOffUse">
					   			<span @click="useWriteOffsPay">立即使用</span>
					   			<p class="notice">使用后不可撤销，请在店员指导下操作</p>
					   		</div>
			    		</div>
			    		<div class="usecouponPay clearfix"  >
							<div class="home fl w40 " @click="qrCodePay">
								<p> <i class="icon  iconfont icon-erweima" :class="{vipcPayactive:isqrCode}"></i></p>
								<p :class="{vipcPayactive:isqrCode}">优惠券码</p>
							</div>
							<div class="mycoupon fr w40 " @click="writeOffsPay">
								<p> <i class="icon iconfont icon-zizhu" :class="{vipcPayactive:iswriteOff}"></i></p>
								<p :class="{vipcPayactive:iswriteOff}">自动核销</p>
							</div>
						</div>
		    		</div>
		    		<!-- 购买成功 -->
		    		<div v-show="isSucceed">
		    			<div class="succeed">
		    				<p><i class="icon iconfont icon-chenggong1 vipcPayactive"></i></p>
		    				<p class="vipcPayactive">使用成功</p>
		    				<p class="vipcPayactive">153442542244445</p>
		    			</div>
		    		</div>
		    	</div>
		    </div>
		    
			<mt-popup
			  	v-model="isVipcn"
			  	position="right" class="vipcnWinBg">
			  	<VipcnWind v-on:close-vipcnWind="listenVipcnClose"></VipcnWind>
			</mt-popup>
		</div>
		<mt-popup
		  	v-model="popupVisible"
		  	position="right" class="applywWin areaCodeZindex">
		  	<ApplyStore v-on:close-applyStore="listenApplyClose" :otherApply="otherPos"></ApplyStore>
		</mt-popup>
	   <div class="heightline"></div>
		<CouponFooter v-on:go-whichRoute="goWhich" :nowPos="whichPos"></CouponFooter>
	</div>
</template>

<script>
import CouponFooter from '@/components/CouponFooter'
import VipcnWind from '@/components/VipcnWind'
import ApplyStore from '@/components/ApplyStore'
export default {
	data () {
	    return {
	     	islogin:false,
	     	ismember:false,
	     	hasMessage:true,
	     	isdetial:false,
	     	isVipcn:false,
	     	isqrCode:true,
	    	iswriteOff:false,
	    	isUseWin:false,
	    	isSucceed:false,
	    	otherPos:false,
	     	isOverlayer:false,
	     	popupVisible:false,
	     	whichPos:1
	    }
	},
  	components:{
	    CouponFooter,
	    VipcnWind,
	    ApplyStore
	},
	methods:{
		immediate(){
			var _this=this;
			_this.isUse=true;
			_this.isUseWin=true;
			_this.isqrCode=true;
			_this.isOverlayer=true;
			 window.document.querySelector('.overlayer').setAttribute('style', 'z-index: 2501;')
		},
		qrCodePay(){
			var _this=this;
			_this.isqrCode=true;
			_this.iswriteOff=false;
		},
		useWriteOffsPay(){
			var _this=this;
			_this.isSucceed=true;
		},
		writeOffsPay(){
			var _this=this;
			_this.isqrCode=false;
			_this.iswriteOff=true;
		},
		cancel(){
			var _this=this;
			_this.isqrCode=false;
			_this.iswriteOff=false;
			_this.isUseWin=false;
			_this.isOverlayer=false;
			_this.isSucceed=false;
		},
		applystore(cur){
			var _this=this;
			 window.document.querySelector('body').setAttribute('style', 'overflow: hidden;')
			if(cur==1){
				_this.otherPos=true;
			}else{
				_this.otherPos=false;
			}
			_this.popupVisible=true;	
			_this.isOverlayer=true;	
		},
		listenApplyClose(data){
			var _this=this;
			// console.log(data)
			window.document.querySelector('body').setAttribute('style', 'background:#fffae1')
			if(data==1){
				_this.popupVisible=false;
				
				setTimeout(function(){
					_this.isOverlayer=false;
				},300)
			}
			if(data==2){
				_this.popupVisible=false;
			}
		},
		lookOver(){
			var _this=this;
			_this.isVipcn=true;
			_this.isOverlayer=true;
			window.document.querySelector('.overlayer').setAttribute('style', 'z-index: 10;')
		},
		listenVipcnClose(data){
			var _this=this;
			if(data){
				_this.isVipcn=false;
				setTimeout(function(){
					_this.isOverlayer=false;
				},300)
			}
		},
		goWhich(pos){
			var _this=this;
			if(pos){
				_this.whichPos=pos;
			}
		}
	},
	beforeCreate () {
	    window.document.querySelector('body').setAttribute('style', 'background:#fffae1')
	},
	beforeDestroy () {
	    // window.document.querySelector('body').setAttribute('style', '')
	}
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>

</style>



