<template>
	<div class="usecoupon">
		<div class="clearfix topuphead">
			<a @click="$router.go(-1)" class="ago fl"><i class="icon iconfont icon-left_atn"></i></a>
		</div>
	   	<div class='useCTitle'>
	   		<div class='useCTitleOne'>
	   			<p class="useCTitleP">
	   				<img :src="imgPath+curCardData.loginUrl" class="useCTitlePImg" v-show="jiazaiOk">
	   				<span class="useCTitlePT">{{curCardData.brandName}}</span>
	   			</p>
	   			<span class="circleLeft" :style='"background:"+bgcolor'></span>
	   			<span class="circleRight" :style='"background:"+bgcolor'></span>
	   		</div>
	   		<div class="useCImg">
	   			<img :src="imgPath+curCardData.iconUrlList" v-show="jiazaiOk">
	   			<p class="useCImgDetial">{{curCardData.summary}}<span class="useCImgIcon fr" @click="detailPage"> <i class="icon iconfont icon-right_atn"></i></span></p>
	   		</div>
	   		<div class="useCContent clearfix">
	   			<p>{{curCardData.title}}</p>
	   			<p class="useCContentT" v-if="curCardData.cardType==2&&curCardData.cashLeastCost>0">消费满{{curCardData.cashLeastCost}}元可减{{curCardData.reduceCost}}元</p>
	   			<p class="useCContentT" v-if="curCardData.cardType==4">{{curCardData.defaultDetail}}</p>
	   			<p class="useCContentT" v-if="curCardData.gift!=null">{{curCardData.gift}}</p>
	   			<div class=" clearfix shareother" :class="[(isMe==true)?'fl':'']">
		   			<span @click="immediate" v-show="!isUseSucceed" :style='"background:"+bgcolor'>立即使用</span>
		   			<span v-show="isUseSucceed" :style='"background:"+bgcolor' class="hasuse">已使用</span>
	   			</div>
	   			<div class="fl clearfix" v-if="isMe" @click="shareOtherUse">
	   				<span :style='"color:"+bgcolor+";border: 1px solid"+bgcolor' class="shareButton">分享他人</span>
	   			</div>
	   		</div>
	   		<div class="conDetial useCouponDetial">
				<ul class="conDetialul" v-if="duofenCardTimeData==null">
					<li>{{duofenCardGetNewData.startTime}}至{{duofenCardGetNewData.endTime}}</li>
					<li v-if="curCardData.useType==0">全场通用</li>
					<li v-if="curCardData.useTime==0">全时段可用</li>
					<li v-if="curCardData.gift!=null">{{curCardData.gift}}</li>
				</ul>
				<ul class="conDetialul" v-else>
					<li>{{duofenCardGetNewData.startTime}}至{{duofenCardGetNewData.endTime}}</li>
					<li v-if="curCardData.useType==0">全场通用</li>
					<li v-if="curCardData.useTime==0">全时段可用</li>
					<li v-if="curCardData.useTime==1"> {{duofenCardTimeData.week}}可用</li>
					<li v-if="duofenCardTimeData.holidays==1">法定节假日不可用</li>
					<li v-if="duofenCardTimeData.otherTime==1" v-html="duofenCardTimeData.otherTimeSets"></li>
					<li v-if="curCardData.gift!=null">{{curCardData.gift}}</li>
				</ul>
			</div>
			<div class="conponMenu">
				<div class="menuP1 clearfix" v-if="isMe||isNoShare">
					<span>推荐朋友领券</span>
					<span class="fr"  @click="recommend"><i class="icon iconfont icon-right_atn"></i></span>
				</div>
				<div class="menuP1 clearfix" >
					<span>适用门店</span>
					<span class="fr" @click="applystore"><i class="icon iconfont icon-right_atn"></i></span>
				</div>
				<div class="menuP1 clearfix">
					<div class="w55 fl phone">
						<p class="title">客服电话</p>
						<p class="telephone">{{curCardData.servicePhone}}</p>
					</div>
					<div class="fr phoneImg w20">
						<a :href="'tel:'+curCardData.servicePhone">	
							<span> <i class="icon iconfont icon-iconfontdianhua" :style='"color:"+bgcolor'></i></span>
						</a>
					</div>
				</div>
				<div class="menuP1 clearfix">
					<span>公众号</span>
					<span class="fr" @click="publicNumber"><i class="icon iconfont icon-right_atn"></i></span>
				</div>
			</div>
	   	</div>
	   	<!-- 购买弹窗 -->
	   	 <div class="overlayer" @touchmove.prevent v-show="isOverlayer">
		   	<div class="popupWin useWin useWinZindex" v-show="isUseWin">
		    	<div class="popupWinCo useWinCo">
		    		<div class="close clearfix">
		    			<p @click="cancel" :style='"color:"+bgcolor'> 取消</p>
		    			<div class='useCTitleOne'>
				   			<p class="useCTitleOnep1">{{useCardData.brandName}}</p>
				   			<p class="useCTitleOnep2">{{useCardData.title}}</p>
				   			<span class="circleLeft"></span>
				   			<span class="circleRight"></span>
				   		</div>
		    		</div>
		    		<!-- 购买方式 -->
		    		<div v-show="!isSucceed">
			    		<div class="qrCode" v-show="isqrCode">
			    			<p class="qrCodeP1">请向店员出示以下优惠券二维码</p>
			    			<img class="qrCodeImg useCouponImg" :src="qrCodeImg">
			    			<p class="qrCodeP2">{{cardCode}}</p>
			    		</div>
			    		<div v-show="iswriteOff">
							<div class="menuP1 writeOffs clearfix">
								<span>{{shopbusinessName}}</span>
								<span class="fr"  @click="applystore(1)"><i class="icon iconfont icon-right_atn"></i></span>
							</div>
							<div class="useCContent">
					   			<span @click="useWriteOffsPay" :style='"background:"+bgcolor'>立即使用</span>
					   			<p class="notice">使用后不可撤销，请在店员指导下操作</p>
					   		</div>
			    		</div>
			    		<div class="usecouponPay clearfix"  >
							<div class="home fl w40 " @click="qrCodePay">
								<div v-show="isqrCode">
									<p> <i class="icon  iconfont icon-erweima" :style='"color:"+bgcolor'></i></p>
									<p :style='"color:"+bgcolor'>优惠券码</p>
								</div>
								<div v-show="!isqrCode">
									<p> <i class="icon  iconfont icon-erweima" ></i></p>
									<p >优惠券码</p>
								</div>
							</div>
							<div class="mycoupon fr w40 " @click="writeOffsPay">
								<div v-show="iswriteOff">
									<p> <i class="icon iconfont icon-zizhu" :style='"color:"+bgcolor'></i></p>
									<p :style='"color:"+bgcolor'>自动核销</p>
								</div>
								<!-- v-if="useCardData.isOwnUse==1" -->
								<div v-show="!iswriteOff" >
									<p> <i class="icon iconfont icon-zizhu" ></i></p>
									<p >自动核销</p>
								</div>
							</div>
						</div>
		    		</div>
		    		<!-- 购买成功 -->
		    		<div v-show="isSucceed">
		    			<div class="succeed">
		    				<p><i class="icon iconfont icon-chenggong1" :style='"color:"+bgcolor'></i></p>
		    				<p :style='"color:"+bgcolor'>使用成功</p>
		    				<p :style='"color:"+bgcolor'>{{cardCode}}</p>
		    			</div>
		    		</div>
		    	</div>
		    </div>
		   
			<mt-popup
			  	v-model="isVipcn"
			  	position="right" class="vipcnWinBg">
			  	<VipcnWind v-on:close-vipcnWind="listenVipcnClose" :guanzhuUrl="guanzhuUrl"></VipcnWind>
			</mt-popup>
		</div>
		 <mt-popup
			  v-model="popupVisible"
			  position="right" class="applywWin">
			  	<ApplyStore v-on:close-applyStore="listenApplyClose" v-on:choose-Store="curApplyClose"  :otherApply="otherPos"></ApplyStore>
			</mt-popup> 
		<mt-popup
		  v-model="sharePos"
		  position="right" class="shareWin" >
		  	<div class="shareWinButton" @click="closeShareWin"></div>
		</mt-popup>
	</div>
</template>

<script>
import CouponFooter from '@/components/CouponFooter'
import ApplyStore from '@/components/ApplyStore'
import VipcnWind from '@/components/VipcnWind'
import Lib from '@/assets/js/Lib';
export default {
	data () {
	    return {
	    	isqrCode:true,
	    	iswriteOff:false,
	    	isUseWin:false,
	    	isSucceed:false,
	    	popupVisible:false,
	    	isOverlayer:false,
	    	otherPos:false,
	    	isOwn:0,
	    	isVipcn:false,
	    	jiazaiOk:false,
	    	sharePos:false,
	    	isNoShare:false,
	    	cardId:0,
	    	busId:0,
	    	isMe:false,
	    	url:'',
	    	curCardData:[],
	    	canShare:0,
	    	duofenCardTimeData:[],
	    	duofenCardGetNewData:[],
	    	isUseSucceed:false,
	    	useCardData:[],
	    	bgcolor:'#764ba5',
	    	imgPath:'',
	    	cardCode:'',
	    	qrCodeImg:'',
	    	guanzhuUrl:'',
	    	shopbusinessName:'选择你所在的门店',
	    	shopId:'',
	    	host:'',
	    	wxshareData:[],
	    	share:{
	    		urlShare:'',
		    	shareTitle:'您的好友给您推荐一张优惠券，赶紧领取吧!',
		    	shareDesc:'识别图中二维码领取吧!',
		    	shareImg:'http://wx.qlogo.cn/mmopen/ib5Ef9ImF1HN9hPsiaBlibGe5iaQDGFtJ4J33IicwtlB8YxSWzfLrmBNqjsdqOUM3MFtJpuZRdpNhwIqguw0D5ibOhxaFKBlsP20De/0'
	    	}

	    }
	},
  	components:{
	    CouponFooter,
	    ApplyStore,
	    VipcnWind
	},
	methods:{
		loadData(){
			var _this=this,json={};
			_this.busId= _this.$route.params.busId;
			_this.cardId= _this.$route.query.cardId;
			_this.url=window.location.href;
			json["busId"]=_this.busId;
			json["requestUrl"]=_this.url;
			json["receiveId"]=_this.cardId;
			json["wxshareUrl"]=_this.url;
			_this.share.urlShare=_this.url;
			Lib.M.ajax({
				'url':memberAPI.activeAPI.useDuofenCardByCardId_POST,
                'type': 'POST',
                'data':{
                   "json":json
                },
                'success':function (data){
                	if(data.code==0){
	                	_this.isOwn=data.data.isOwn;
	                	_this.canShare=data.data.duofenCardNew.canShare;
	                	if(_this.canShare==1){//优惠券能分享
		                	if(_this.isOwn==0){//本人
		                		_this.isMe=true;
		                	}
	                	}else{
	                		_this.isNoShare=true;
	                	}
                		_this.curCardData=data.data.duofenCardNew;
	                	_this.duofenCardTimeData=data.data.duofenCardTime;
	                	_this.duofenCardGetNewData=data.data.duofenCardGetNew;
	                	_this.bgcolor=_this.curCardData.color;
	                	_this.imgPath=data.data.path;

	                	_this.host=data.data.host;
	                	_this.guanzhuUrl=data.data.guanzhuUrl;
	                	window.document.querySelector('body').setAttribute('style', 'background:'+_this.bgcolor);
                		_this.duofenCardTimeData=Lib.M.weekSwitch(_this.duofenCardTimeData,_this.curCardData.useTime)
                		_this.duofenCardGetNewData.startTime=Lib.M.formatTime(_this.duofenCardGetNewData.startTime,true);
                		_this.duofenCardGetNewData.endTime=Lib.M.formatTime(_this.duofenCardGetNewData.endTime,true);
                		Lib.M.ajax({
							'url':memberAPI.activeAPI.useVerificationDuofenCard_POST,
			                'type': 'POST',
			                'data':{
			                   "json":json
			                },
			                'success':function (data){
			                	if(data.code==0){
			                		_this.useCardData=data.data.duofenCardNew;
			                		_this.cardCode=data.data.code;
			                		_this.qrCodeImg=memberAPI.activeAPI.homeUrl+memberAPI.activeAPI.useVerificationQrcode_GET+"?code="+_this.cardCode+"&busId="+_this.busId;
			                		Lib.M.ajax({
								      	'url': memberAPI.activeAPI.wxshareCard_POST,
								      	'type': 'POST',
								      	'data': {
								        	"json": json
								      	},
					      				'success': function(data) {
					      					_this.wxshareData=data.data;
					      				},
					      				'error':function(data){
					      				}
					      			})
									//推送
							        var socket =  io.connect(_this.host);
							        var socketIndex=0;//重连次数
							        socket.on('connect', function() {//当与对方建立连接后自动触发 connect 事件
							        	socketIndex=0;
							            // var jsonObject = {userId: userId,
							            //     message: "0"};
							            // socket.emit('auth', jsonObject);
							        });

							        socket.on('chatevent', function(data) {
							        	console.log("通信",data)
							        	if(data){
							        		_this.isUseSucceed=true;
							        	}
							            // sendMessage(data.message,data.style);
							        });

							        socket.on('disconnect', function() {//当对方关闭连接后触发 disconnect 事件。

							        });
							        //重连机制
							        socket.on('reconnecting', function() {
							        	socketIndex+=1;
							        	if(socketIndex>4){
							        		socket.destroy();//不在链接
							        	}
							        });
			                	}
			                	console.log("立即使用",data)
			                	
			                },
			                'warning':function(err){
			                	Lib.M.tipsMsg(err.msg);
			                },
			                'error':function(err){
			                	Lib.M.tipsMsg('请求数据失败');
			                }

			            })
                		_this.jiazaiOk=true;
                	}
                	console.log("使用",data)
                	
                },
                'warning':function(err){
                	Lib.M.tipsMsg(err.msg);
                },
                'error':function(err){
                	Lib.M.tipsMsg('请求数据失败');
                }

            })
		},
		immediate(){
			var _this=this;
			_this.isUse=true;
			_this.isUseWin=true;
			_this.isqrCode=true;
			_this.isOverlayer=true;
		},
		qrCodePay(){
			var _this=this;
			_this.isqrCode=true;
			_this.iswriteOff=false;
		},
		useWriteOffsPay(){
			var _this=this,json={};
			json["busId"]=_this.busId;
			json["requestUrl"]=_this.url;
			json["receiveId"]=_this.cardId;
			json["shopId"]=_this.shopId;
			Lib.M.ajax({
				'url':memberAPI.activeAPI.verificationDuofenCardGet_POST,
                'type': 'POST',
                'data':{
                   "json":json
                },
                'success':function (data){
                	if(data.code==0){
                		_this.isSucceed=true;
                		_this.isUseSucceed=true;
                	}
                	console.log("核销使用",data)
                	
                },
                'warning':function(err){
                	Lib.M.tipsMsg(err.msg);
                },
                'error':function(err){
                	Lib.M.tipsMsg('请求数据失败');
                }

            })
			
		},
		writeOffsPay(){
			var _this=this;
			_this.isqrCode=false;
			_this.iswriteOff=true;
		},
		cancel(){
			var _this=this;
			_this.isqrCode=false;
			_this.iswriteOff=false;
			_this.isUseWin=false;
			_this.isOverlayer=false;
			_this.isSucceed=false;
		},
		recommend(){
			var _this=this;
			_this.$router.push({name:'recommend',params:{busId:_this.busId},query:{receiveId:_this.cardId}})	
		},
		shareOtherUse(){
			var _this=this;
			_this.sharePos=true;
		},
		closeShareWin(){
			var _this=this;
			_this.sharePos=false;
		},
		applystore(cur){
			var _this=this;
			 window.document.querySelector('body').setAttribute('style', 'overflow: hidden;')
			if(cur==1){
				_this.otherPos=true;
			}else{
				_this.otherPos=false;
			}
			_this.popupVisible=true;	
			_this.isOverlayer=true;	
		},
		listenApplyClose(data){
			var _this=this;
			// console.log(data)
			window.document.querySelector('body').setAttribute('style', 'background:'+_this.bgcolor)
			if(data==1){
				_this.popupVisible=false;
				
				setTimeout(function(){
					_this.isOverlayer=false;
				},300)
			}
			if(data==2){
				_this.popupVisible=false;
			}
		},
		curApplyClose(data){
			var _this=this;
			window.document.querySelector('body').setAttribute('style', 'background:'+_this.bgcolor)
			if(data[0]==1){
				_this.popupVisible=false;//购买下适用门店
				setTimeout(function(){
					_this.isOverlayer=false;
				},300)
			}
			if(data[0]==2){
				_this.shopbusinessName=data[1];
				_this.shopId=data[2];
				_this.popupVisible=false;//核销
			}
		},
		publicNumber(){
			var _this=this;
			_this.isVipcn=true;
			_this.isOverlayer=true;
		},
		listenVipcnClose(data){
			var _this=this;
			if(data){
				_this.isVipcn=false;
				setTimeout(function(){
					_this.isOverlayer=false;
				},300)
			}
		},
		detailPage(){
			var _this=this;
			_this.$router.push({ name: 'coupondetail',params:{busId:_this.busId},query:{cardId:_this.cardId}});
		}
	},


	created:function(){
		var _this=this;
		var isfirst=window.localStorage.getItem('share');
			if(isfirst==null){
				isfirst=0;
			}
	    	if(isfirst==0){
	    		location.reload()
	    		window.localStorage.setItem('share',1)
	    	}
        _this.loadData();

    },
    watch:{
		wxshareData(newValue, oldValue){
			var _this=this;
			wx.config({
	            debug: false, // 开启调试模式,调用的所有api的返回值会在客户端alert出来，若要查看传入的参数，可以在pc端打开，参数信息会通过log打出，仅在pc端时才会打印。
	            appId: newValue.appid, // 必填，公众号的唯一标识
	            timestamp: newValue.timestamp, // 必填，生成签名的时间戳
	            nonceStr: newValue.nonce_str, // 必填，生成签名的随机串
	            signature: newValue.signature, // 必填，签名，见附录1
	            jsApiList: ["onMenuShareAppMessage", 'onMenuShareTimeline'] // 必填，需要使用的JS接口列表，所有JS接口列表见附录2
	        });
			wx.ready(function() {
	            //分享给朋友
	           if(_this.canShare==0||_this.isOwn==1){//不能分享
					wx.hideOptionMenu();
				}
				if(_this.canShare==1&&_this.isOwn==0){//能分享
					wx.onMenuShareAppMessage({
			            title: _this.share.shareTitle, // 分享标题
			            desc: _this.share.shareDesc, // 分享描述
			            link: _this.share.urlShare, // 分享链接
			            imgUrl: _this.share.shareImg, // 分享图标
			            success: function() {
			                // 用户确认分享后执行的回调函数
			                Lib.M.tipsMsg('分享成功')
			            },
			            cancel: function() {
			                // 用户取消分享后执行的回调函数
			                 Lib.M.tipsMsg('取消分享')
			            },
			            error: function(res) {
			            }
		            });
		            //分享到朋友圈
		            wx.onMenuShareTimeline({
		             	title: _this.share.shareTitle, // 分享标题
		             	desc: _this.share.shareDesc, // 分享描述
		              	link: _this.share.urlShare, // 分享链接
		              	imgUrl: _this.share.shareImg, // 分享图标
		              	success: function() {
		              		 Lib.M.tipsMsg('分享成功')
		                // 用户确认分享后执行的回调函数
		              	},
		              	cancel: function() {
		              		Lib.M.tipsMsg('取消分享')
		                // 用户取消分享后执行的回调函数
		              	}
		            });
				}
	            
	        });
		}
	},
	beforeCreate () {
	    window.document.querySelector('body').setAttribute('style', 'background:#764ba5')
	},
	destroyed () {
		window.localStorage.setItem('share',0)
	    // window.document.querySelector('body').setAttribute('style', '')
	}
}

</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>

</style>



