
<template>
	<div class="recommenddetail">
	    <div class="rcmDTile"> </div>
	    <div class="rcmDCom clearfix">
	    	<div class="title">
	    		<div class="w40 fl " :class='{rcmactive:1===isShow}' @click="clickMenu(1)">推荐明细</div>
	    		<div class="w40 fr" :class='{rcmactive:2===isShow}'  @click="clickMenu(2)">提现</div>
	    		<span class="line" :class="[(isanimation==true)?'lineaimation':'lineleaveaimation']"></span>
	    	</div>
	    	<div class="content clearfix">
	    		<div v-show="isWithdraw">
		    		<div class='rcmDComP1'>
		    			<p class="rcmDComP1T"><span class="spanLine"></span>我的收益</p>
		    		</div>
		    		<div class="rcmDComP2 clearfix">
		    			<div class="w32 fl part1Div">
		    				<p>{{earningData.number}}人</p>
		    				<p>成功推荐</p>
		    			</div>
		    			<div class="w32 fl part1Div">
		    				<p>{{earningData.successNum}}人</p>
		    				<p>实际用券</p>
		    			</div>
		    			<div class="w32 fl part1Div">
		    				<p>{{earningData.totalMoney}}元</p>
		    				<p>累计金额</p>
		    			</div>
		    		</div>
		    		<div class="rcmDComP2  clearfix">
		    			<div class="w32 fl part1Div">
		    				<p>{{earningData.totalFenbi}}粉币</p>
		    				<p>累计粉币</p>
		    			</div>
		    			<div class="w32 fl part1Div">
		    				<p>{{earningData.totalFlow}}M</p>
		    				<p>累计流量</p>
		    			</div>
		    			<div class="w32 fl part1Div">
		    				<p>{{earningData.totalIntegral}}积分</p>
		    				<p>累计积分</p>
		    			</div>
		    		</div>
		    		<div class="rcmDComP3 clearfix">
		    			<div class='rcmDComP1'>
			    			<p class="rcmDComP1T"><span class="spanLine"></span>推荐明细</p>
			    		</div>
			    		<div class="rcmMessage" v-show="hasMessage">
			    			<ul
							 v-infinite-scroll="loadMore"
							 infinite-scroll-disabled="loading"
							 infinite-scroll-distance="3">
							 	<li v-for="(item,index) in allCardData">
							 		<div class="applystoreP1 clearfix">
										<div class=" applystorelf clearfix">
											<span class="spanname">{{item.nickname}}</span>
											<span class="spantime">{{item.datetime}}</span>
										</div>
										<div class="applystorerg">
											<p><span>¥{{item.money}}</span><span>{{item.fenbi}}粉币</span><span>{{item.flow}}M流量</span><span>{{item.integral}}积分</span></p>
										</div>
									</div>
							 	</li>
							 </ul>
							 <p class="hasbottom" :class="{msgType:loading}">{{jiazhai}}</p>
			    		</div>
			    		<!-- 没记录 -->
			    		<div class="rcmNoMessage" v-show="!hasMessage">
			    			 <i class="icon iconfont icon-kong"></i>
			    			 <p>暂无推荐记录</p>
			    		</div>
		    		</div>
	    		</div>
	    		<div class="withdraw" v-show="!isWithdraw">
	    			<div class='rcmDComP1'>
		    			<p class="rcmDComP1T"><span class="spanLine"></span>我的余额</p>
		    			<div class="withdrawlarge clearfix">
		    				<span class="withdrawlargep1"> <i class="icon iconfont icon-yue1"></i><span class="totMoney">¥{{earningData.tuijianMoney}}</span></span>
		    				<span class="withdrawlargep2">{{withdrawMsg}} <i @click="withdrawMoney" class="icon iconfont icon-right_atn"></i></span>
		    			</div>
		    		</div>
		    		<div class='rcmDComP1'>
		    			<p class="rcmDComP1T"><span class="spanLine"></span>提现记录<span class="leiji">累计提现¥{{earningData.pickMoney}}</span></p>
		    		</div>

		    		<div class="withdrawCon" v-show="isWithdrawMessage">
		    			<div class="applystoreP1 clearfix" v-for="(item,index) in earningData.pickList">
							<div class="fl w75 applystorelf">
								<p>提现</p>
								<p>{{item.pickDate}}</p>
							</div>
							<div class="fr w20 applystorerg">
								<p>¥<span>{{item.pickMoney}}</span></p>
							</div>
						</div>
						<p class="hasbottom">已经到底啦~</p>
		    		</div>
		    		<!-- 没记录 -->
		    		<div class="rcmNoMessage" v-show="!isWithdrawMessage">
		    			 <i class="icon iconfont icon-kong"></i>
		    			 <p>暂无推荐记录</p>
		    		</div>
	    		</div>
	    	</div>
	    </div>
	   <div class="heightline"></div>
		<CouponFooter v-on:go-whichRoute="goWhich" :nowPos="whichPos"></CouponFooter>
	</div>
</template>

<script>
import CouponFooter from '@/components/CouponFooter'
import Lib from '@/assets/js/Lib';
export default {
	data () {
	    return {
	     	islogin:true,
	     	ismember:false,
	     	hasMessage:true,
	     	hasDetial:false,
	     	whichPos:2,
	     	isViewLose:false,
	     	hasWithdraw:false,
	     	isWithdrawMessage:false,
	     	isShow:1,
	     	isWithdraw:true,
	     	isanimation:false,
	     	busId:0,
	     	url:'',
	     	page:1,
	     	earningData:[],
	     	allCardData:[],
	     	recommendData:[],
	     	loading:false,
	     	differMoney:0,
	     	isAllow:false,
	     	jiazhai:'>>加载中<<',
	     	withdrawMsg:'可前往提现'
	    }
	},
  	components:{
	    CouponFooter
	},
	methods:{
		loadData(){
			var _this=this,json={};
			_this.busId= _this.$route.params.busId;
			_this.url=window.location.href;
			json["busId"]=_this.busId;
			json["requestUrl"]=_this.url;
			Lib.M.ajax({
				'url':memberAPI.activeAPI.findTuiJianDuofenCard_POST,
                'type': 'POST',
                'data':{
                   "json":json
                },
                'success':function (data){
                	if(data.code==0){
                		_this.earningData=data.data;
                		if(_this.earningData.lessPickMoney==0&&_this.earningData.tuijianMoney>0){
                			_this.isAllow=true;
                			_this.withdrawMsg=`可前往提现`;
                		}
                		if(_this.earningData.lessPickMoney>0){
                			_this.differMoney=_this.earningData.lessPickMoney-_this.earningData.tuijianMoney;
                			if(_this.differMoney<=0){
                				_this.isAllow=true;
                				_this.withdrawMsg=`可前往提现`;
                			}else{
                				_this.isAllow=false;
                				_this.withdrawMsg=`还差¥${_this.differMoney}可提现`;
                			}
                		}
                		if(_this.earningData.pickList!=undefined){
                			if(_this.earningData.pickList.length>0){
	                			_this.isWithdrawMessage=true;
	                			for(let i=0;i<_this.earningData.pickList.length;i++){
	                				_this.earningData.pickList[i].pickDate=Lib.M.formatTime(_this.earningData.pickList[i].pickDate);
	                			}
                			}
                		}
                		_this.getPageData(_this.page);
                	}
                	console.log('推荐详情',data);
                },
                'warning':function(err){
                	Lib.M.tipsMsg("获取失败");
                }
            })

		},
		getPageData(page){
			var _this=this,jsons={};
			jsons["busId"]=_this.busId;
			jsons["requestUrl"]=_this.url;
			jsons["page"]=page;
			Lib.M.ajax({
				'url':memberAPI.activeAPI.findRecommendPage_POST,
                'type': 'POST',
                'data':{
                   "json":jsons
                },
                'success':function (data){
                	if(data.code==0){
                		_this.loading = false;
                		_this.recommendData=data.data;
                		console.log(_this.recommendData)
                		if(_this.recommendData!=undefined){
                			if(_this.recommendData.length>0){

                				for(let i=0;i<_this.recommendData.length;i++){
                					_this.recommendData[i].datetime=Lib.M.formatTime(_this.recommendData[i].datetime);
                					_this.allCardData.push(_this.recommendData[i]);
                				}
                			}
                		}else{
                			_this.hasMessage=false
                		}
                	}else{
                		_this.hasMessage=false
                	}
                	console.log('推荐详情列表',data);
                },
                'warning':function(err){
                	_this.hasMessage=false
                	Lib.M.tipsMsg("获取失败");
                },
                'error':function(err){
                	_this.hasMessage=false
                }
            })
		},
		loadMore() {
			var _this=this,json={};
			if(_this.isWithdraw){
				if(_this.recommendData.length==0||_this.recommendData.length<10){
					_this.loading = false;
					if(_this.page==1){
						return
					}
					_this.jiazhai='已经到底啦~'
					return false
				}else{
					_this.loading = true;
					_this.jiazhai='>>数据获取中<<'
					_this.page+=1;
				}
				_this.getPageData(_this.page);
			}
		},
		login(){
			var _this=this;
			_this.$router.push({ name: 'login'});
		},
		viewDetail(cur){
			var _this=this;
			_this.hasDetial=(_this.hasDetial==cur)?false:cur;
		},
		useCoupon(){
			var _this=this;
			_this.$router.push({ name: 'usecoupon'});
		},
		goWhich(pos){
			var _this=this;
			if(pos){
				_this.whichPos=pos;
			}
		},
		viewlose(){
			var _this=this;
			_this.isViewLose=true;
		},
		clickMenu(index){
			var _this=this;
			_this.isShow=index;
			console.log('pos',index)
			let ind;
			if(index==1){
				_this.isWithdraw=true;
				_this.isanimation=false;

			}else{
				_this.isWithdraw=false;
				_this.isanimation=true;
			}
			// _this.styleObject.transform='translateX('+ind*parseInt(_this.styleObject.width)+'%)';
			
		},
		coupondetail(){
			var _this=this;
			_this.$router.push({ name: 'coupondetail'});
		},
		withdrawMoney(){
			var _this=this;
			if(_this.isAllow){
				_this.$router.push({ name: 'withdraw',params:{busId:_this.busId},query:{tuijianMoney:_this.earningData.tuijianMoney}});
			}else{
				Lib.M.tipsMsg('可提现金额不足!')
			}
		}
	},
	created:function(){
        this.loadData();
    },
	beforeCreate () {
	    window.document.querySelector('body').setAttribute('style', 'background:#fc6a68')
	},
	beforeDestroy () {
	    // window.document.querySelector('body').setAttribute('style', '')
	}
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
	.lineaimation{
		animation:mymove .5s 1 forwards;
		-webkit-animation:mymove .5s 1 forwards; /*Safari and Chrome*/
	}
	.lineleaveaimation{
		animation:myleavemove .5s 1 forwards;
		-webkit-animation:myleavemove .5s 1 forwards; /*Safari and Chrome*/
	}
	@keyframes mymove
	{
	from {left:18%;}
	to {left:58%;}
	}

	@-webkit-keyframes mymove /*Safari and Chrome*/
	{
	from {left:18%;}
	to {left:58%;}
	}
	@keyframes myleavemove
	{
	from {left:58%;}
	to {left:18%;}
	}

	@-webkit-keyframes myleavemove /*Safari and Chrome*/
	{
	from {left:58%;}
	to {left:18%;}
	}
</style>



