<template>
	<div >
		<div class="login">
		    <div class="phoneBindGetCode">
				<i class="areaCode" @click="ChooseAreaCode">+{{currAreaData.currAreaCode}}<i class="icon iconfont icon-at_atn"></i></i>
				<input type="number" @blur="verification()" v-model='phone' placeholder="请输入手机号码" name="">
				
			</div>
			<div class="phoneBindCode clearfix">
				<input type="text" name="" v-model="vcode" value="" @focus="clearInput"  placeholder="请输入验证码" id="authCode" @blur="checkAuthCode">
				<a href="javascript:;" v-if="isGetCode" class="phonemessage">{{timeleft}}s后可重发</a>
				<a href="javascript:;" v-else @click="getCode">获取验证码</a>
			</div>
		</div>
	    <div class="loginCon">
	    	<p>补全帐号信息，即可同步储存，随时查看优惠券</p>
	    </div>
	     <div class="loginbut">
	    	<input :class="[(isAllOk==true)?'isactive':'btDisable']" @click="bindPhone" type="button" name="" value="完成验证">
	    </div>
	    <mt-popup
		  v-model="popupVisible"
		  position="right" class="areaCodeZindex">
		  	<PhoneAreaCode v-on:close-areaCode="listenToClose" v-on:curr-areaCode="getAreaCode"></PhoneAreaCode>
		</mt-popup>
		<CouponFooter v-on:go-whichRoute="goWhich" :nowPos="whichPos"></CouponFooter>
	</div>
</template>

<script>
import CouponFooter from '@/components/CouponFooter';
import PhoneAreaCode from "@/components/PhoneAreaCode";
import Lib from '@/assets/js/Lib';
export default {
	data () {
	    return {
	    	timeleft:120,
	    	isGetCode:false,
	    	vcode:'',
	    	popupVisible:false,
	    	phone:'',
	    	hasTime:'',
	    	busId:0,
	    	url:'',
	    	whichPos:1,
	    	cardId:0,
	    	currAreaData:{
		    	currAreaCode:'86',
		    	currId:1
		    },
		    tipMsg:{
		    	isphoneCodeMsg:'请获取验证码',
		    	isphoneMsg:'请输入正确的手机号码',
		    	isBuild:'请填写手机号码与验证码'
		    },
		    isOk:{
		    	isphone:false,
		    	isCode:false
		    },
		    isAllOk:false,
	    }
	},
  	components:{
	    CouponFooter,
	    PhoneAreaCode
	},
	methods:{
		loginData(){
			var _this=this;
			_this.busId= _this.$route.params.busId;
			_this.cardId= _this.$route.query.cardId;
			_this.tuijianCode= _this.$route.query.tuijianCode;
			_this.url=window.location.href;
			_this.isAllOk=Lib.M.checkPickClub(_this.isOk);
		},
		checkAuthCode(){
			var _this=this;
			if(_this.isOk.isphone==false){
				Lib.M.tipsMsg(_this.tipMsg.isphoneMsg);
				return;
			}
			if(_this.vcode!=''){
				if(_this.isGetCode==false){
					Lib.M.tipsMsg('请获取验证码');
					return
				}
				_this.isOk.isCode=true;
				_this.isAllOk=Lib.M.checkPickClub(_this.isOk);
			}else{
				Lib.M.tipsMsg(_this.tipMsg.isphoneCodeMsg);
				_this.isOk.isCode=false;
				_this.isAllOk=Lib.M.checkPickClub(_this.isOk);
			}
		},
		clearInput(){
			var _this=this;
			_this.vcode='';
		},
		// 手机号码验证
		verification(){
			var _this=this;

			if(_this.currAreaData.currAreaCode=='86'){
				_this.isTrue=Mobilephone(_this.phone);
				if(!_this.isTrue){
					Lib.M.tipsMsg(_this.tipMsg.isphoneMsg);
					_this.isOk.isphone=false;
					_this.isAllOk=Lib.M.checkPickClub(_this.isOk);
				}else{
					_this.isOk.isphone=true;
					_this.isAllOk=Lib.M.checkPickClub(_this.isOk);
				}
			}else{
				_this.checkOption.phoneOption=true;
				_this.isAllOk=Lib.M.checkPickClub(_this.isOk);
			}
		},
		getCode(){
			var _this=this,json={};
			if(_this.isOk.isphone==false){
				Lib.M.tipsMsg(_this.tipMsg.isphoneMsg);
				return;
			}
			json["busId"]=_this.busId;
			json["phone"]=_this.phone;
			json["country"]=_this.currAreaData.currAreaCode;
			_this.isGetCode=true;
			Lib.M.ajax({
				'url':memberAPI.activeAPI.sendMsg_POST,
                'type': 'POST',
                'data':{
                   "json":json
                },
                'success':function (data){
                	// Lib.M.tipsMsg('验证码:'+data.data,8000);
                	console.log("phonedata",data);
                }

			})
			if(_this.isGetCode){
				_this.hasTime=setInterval(function(){
					_this.timeleft--;
					if(_this.timeleft==0){
						clearInterval(_this.hasTime);
						_this.timeleft=120;
						_this.isGetCode=false;
						_this.isOk.isCode=false;
						_this.isAllOk=Lib.M.checkPickClub(_this.isOk);
					}
				},1000)
			}

		},
		//完成验证
		bindPhone(){
			var _this=this,json={};
			if(!_this.isAllOk){
				Lib.M.tipsMsg(_this.tipMsg.isBuild);
				return;
			}
			json["busId"]=_this.busId;
			json["requestUrl"]=_this.url;
			json["phone"]=_this.phone;
			json["vcode"]=_this.vcode;
			json["country"]=_this.currAreaData.currAreaCode;
			Lib.M.ajax({
				'url':memberAPI.activeAPI.bingdingPhone_POST,
                'type': 'POST',
                'data':{
                   "json":json
                },
                'success':function (data){
                	// Lib.M.tipsMsg('验证码:'+data.data,8000);
                	if(data.code==0){
                		var _this=this,jsons={};
						jsons["busId"]=_this.busId;
						jsons["requestUrl"]=_this.url;
						jsons["cardId"]=_this.cardId;
						Lib.M.ajax({
							'url':memberAPI.activeAPI.getDuofenCard_POST,
			                'type': 'POST',
			                'data':{
			                   "json":jsons
			                },
			                'success':function (data){
			                	if(data.code==0){
			                		_this.$router.push({ name: 'home',params:{busId:_this.busId}});
			                		Lib.M.tipsMsg('领取成功');
			                	}
			                },
			                'warning':function(err){
			                	if(err.code==6007){
			                		_this.$router.push({ name: 'closeaccount',params:{busId:_this.busId},query:{cardId:_this.cardId,tuijianCode:_this.tuijianCode}});
			                	}
			                }
			            })
	                	clearInterval(_this.hasTime);
						_this.timeleft=120;
						_this.isGetCode=false;
                	}
                	console.log("bingdingPhone",data);
                },
                'warning':function(err){
                	clearInterval(_this.hasTime);
					_this.timeleft=120;
					_this.isGetCode=false;
					_this.isOk.isCode=false;
					_this.isAllOk=Lib.M.checkPickClub(_this.isOk);
                },
                'error':function(err){
                	clearInterval(_this.hasTime);
					_this.timeleft=120;
					_this.isGetCode=false;
					_this.isOk.isCode=false;
					_this.isAllOk=Lib.M.checkPickClub(_this.isOk);
                }

			})
		},
		// 区号选择
		ChooseAreaCode(){
			var _this=this;
			_this.popupVisible=true;
		},
		listenToClose:function(data){
			var _this=this;
			if(data){
				_this.popupVisible=false;
			}
		},
		getAreaCode:function(aCode){
			var _this=this;
			_this.popupVisible=false;
			_this.currAreaData=aCode;
			console.log(_this.currAreaData);
		},
		goWhich(pos){
			var _this=this;
			if(pos){
				_this.whichPos=pos;
			}
		}

	},
	created () {
		var _this=this;
		_this.loginData();
	    window.document.querySelector('body').setAttribute('style', 'background:#f1f1f1')
	},
	beforeDestroy () {
	    // document.querySelector('body').setAttribute('style', '')
	}
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
	.login #authCode{
		padding-left: 1.5rem;
	}
</style>



